<template>
    <div class="pagination">
        <ul class="pagination-items">
            <li ref='dot' class="pagination-item" v-for="num, i in totalPage" :key="i" @click="setActive(i)">
                <p class="golos-m">{{ num }}</p>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name:'a-pagination',
        props:{
            allItems:[Array, Number],
            showItems:[Array, Number],     
        },
        data(){
            return{
               pageNumber:1,
            }
        },
        methods:{
            setActive(id){
                this.$refs.dot.map(e => e.classList.remove('active'))
                this.$refs.dot[id].classList.add('active')
                this.pageNumber = +this.$refs.dot[id].innerText


                this.$emit('check-num', this.pageNumber)
            }
        },
        mounted(){
            this.$refs.dot[0].classList.add('active')
            this.$emit('check-num', this.pageNumber)
        },
        computed:{
            totalPage(){
                return Math.ceil(this.allItems.length / this.showItems.length)
            },
            
        }
    }
</script>

<style scoped>
    .pagination-items{
        display: flex;
        gap: 5px;
        justify-content: center;
    }
    .pagination-item{
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        border-radius: 50%;
        padding: 10px;
        width: 44px;
        height: 44px;
        transition: background-color .2s ease-out;

    }
    .pagination-item.active{
        background-color: #F56E1E;
        
    }
    .pagination-item.active p{
        color: #fff;
    }

    .pagination-item:hover{
        outline: 1px solid #F56E1E;
    }
   
</style>