                        <div class="header-main__label"></div>
                    </div>
                </router-link>
                <div class="header-bottom__item header__catalog">
                    <a-catalog @catalogItems="getCatalogList"  @showOverlay="checkHeadOverlay" @hideOverlay="checkHideOverlay"></a-catalog>
                </div>
                <div class="header-bottom__item header-cart">
                    <router-link to="/homepage/lk/cart">
                        <a-cart></a-cart>
                    </router-link>
                </div>
                <div class="header-bottom__item header-fav">
                    <a-fav></a-fav> 
