    </header>
    <a-submenu class="subheader" :menuActive="menuActive">
        <div class="description">
            <p class="description__text golos-m">
                . . .
            </p>
        </div>
        <a-region class="subheader-region"></a-region>
        <a-submenulist class="subheader-submenulist"></a-submenulist>
        <a-society class="subheader-society"></a-society>
        <a-phone class="subheader-phone"></a-phone>
    </a-submenu>
    <div class="header-menu-overlay" v-show="isActive" ></div>
    
</template>

<script>
import ASubMenu from './UI/ASubMenu.vue';

    export default {
    components: {
        'a-submenu':ASubMenu,
    },
    data() {
      return {
        scrolled: false,
        isActive: false,
        signActive:false,
        menuActive:false,
        catalogList:[]
       
       
      }
    },
    methods:{
        scroll(){
            const scrollPosition = window.scrollY;
            if (scrollPosition > 0) {
                this.scrolled = true;
            }else{
                this.scrolled = false;
            }
        },
        checkHeadOverlay(){
            this.isActive = !this.isActive
        },
        checkHideOverlay(){
            this.isActive = false
        },
        showMenu(){
            this.menuActive = !this.menuActive
            if (this.menuActive){
                    document.body.classList.add('_lock')
                }else{
                    document.body.classList.remove('_lock')
                }
        },
        getCatalogList(arr){
            this.catalogList = arr;
        }
        
    },
    created(){
        window.addEventListener('scroll', this.scroll);
       
    },
   
 
    
    }
</script>

<style scoped>
.burger-item{
  
    position: relative;
    width:24px ;
    height: 18px;
    
}
.burger-item::before,
.burger-item::after{
    transition: all 0.3s ease;
    content: '';
    width: 100%;
    height: 2px;
    position: absolute;
    background-color: #F56E1E;
}

.burger-item::after{
    bottom: 0;
}
.burger-item::before{
    top: 0;
}

.burger-item span{
    position: absolute;
    background-color: #F56E1E;
    top: 45%;
    width: 100%;
    height: 2px;
}

.header__burger.opened .burger-item span{
    height: 0;
}

.header__burger.opened .burger-item::after{
    transform: rotate(-45deg);
    bottom: 8px;
}

.header__burger.opened .burger-item::before{
    transform: rotate(45deg);
    top: 8px;
}
.sign-overlay{
    background-color: rgba(0, 0, 0, 0.2);
    position: fixed;
    z-index: 1000;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
.header-menu-overlay{
    background-color: rgba(0, 0, 0, 0.2);
    position: fixed;
    z-index: 98;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
}
.header{
 transition: all 0.5s ease;
 position: fixed;
 top: 0;
 left: 0;
 width: 100%;
 padding: 9px 0;
 z-index: 99;
 background-color: #FFFFFF;
}

.header-bottom{
    display: none;
}
.header__inner{
    display: flex;
    align-items: center;
    position: relative;
    gap: 30px;
}
.header__search{
    flex:1 1 auto;
}

.header__user-items{
    display: flex;
    gap: 30px;
    align-items: center;
    text-align: center;
}



.scrolled{
    
    box-shadow: 0px 2px 16px rgba(0, 0, 0, 0.2);
}




.description{
    border-radius: 40px;
    background: #F1F1F1;   
    padding: 8px 12px; 

}

.description__text{
    text-align: center;
}

@media(max-width:1299px){
    .description{
        display: none;
    }
}

@media (max-width:1024px){
   .header__inner,  .header__user-items{
    gap: 15px;
   }
 
   
}

@media (min-width: 768px) {
    .subheader{
        margin-top: 90px;
        margin-bottom: 17px;
    }
}

@media (max-width:768px) {
    .header{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        background: #FFFFFF;
    }

    .header-menu-overlay{
        opacity: 0;
        left: -100%;
    }
    .header__inner{
       
        justify-content: space-around;
      
    }
    .header__user-items,
    .header__catalog{
    display: none;
    }
    .header__search{
        flex: 0 0 auto;
    }


    .header-bottom{
        display: block;
        position: fixed;
        padding: 10px 5px;
        width: 100%;
        bottom: 0;
        left: 0;
        z-index: 999;
        background-color: white;
        box-shadow: 0 -2px 5px 0 rgba(0, 0, 0, 0.2);
    }

    .header-bottom__items{
        gap: 5px;
        display: flex;
        align-items: end;
        justify-content: space-between;
    }
    .header-bottom__item{
     
       display: flex;
       flex-direction: column;
       justify-content: center;
       align-items: center;
    }

    .header-bottom__item:hover path{
        stroke: #F56E1E;
        fill: #F56E1E;
    }

    .header-bottom__item:hover {
        color: #F56E1E;
    }


    .subheader-region{
        order: 2;
    }

    .subheader-submenulist{
        order: 1;
    }
    .subheader-phone{
        order: 3;
    }

    .subheader-society{
        order: 4;
    }

    @media (max-width:399px){
        .header-main__icon svg{
            width: 18px;
            height: 18px;
        }
        .header-main__label{
            font-size: 9px;
        }
        
    }
}

</style>