<template>
    <div class="slider">
        <slot></slot>
        <div class="slider__container">
            <ul v-if="slideOne_data.length > 0" class="slider__items" :style=" {gap:sliderMargin +'px'}">
                <a-slider-item 
                    class="slider__item"
                    style="transition: all 0.5s ease;"
                    v-for="slide in slideOne_data"
                    :key="slide.id"
                    :sliderOneItem="slide"
                    :style="{transform:'translateX(calc((-100% - '+ sliderMargin + 'px) * ' + currentSlide + '))', flex:'0 0 calc('+ slideWithMargin +'%'+ ' - ' + sliderMargin + 'px)'}"
                ></a-slider-item>
            </ul>
            <ul v-else class="slider__sellitems" :style=" {gap:sliderMargin +'px'}">
                <a-sell-item v-for="item in sellItems" 
                :key="item.id" 
                :sellItem="item"
                :style="{transform:'translateX(calc((-100% - '+ sliderMargin + 'px) * ' + currentSlide + '))', flex:'0 0 calc('+ slideWithMargin +'%'+ ' - ' + sliderMargin + 'px)'}"
                style="transition: all 0.5s ease;"
                ></a-sell-item>
            </ul>
            <div class="slider__buttons">
                <div class="slider__button">
                    <a-slideprev 
                    @click="prevSlide"  
                     ></a-slideprev>
                </div>
                <div class="slider__button">
                    <a-slidenext 
                    @click="nextSlide">
                    </a-slidenext>
                </div>
            </div>
        </div>
        <a-slide-pagination
            v-if="slideOne_data.length > 0"
            class="slider__pagination"
            :sliderItems="slideOne_data"
            :pageCount="pageCount"
            :currentSlider="currentSlide" 
            @slide-changed="updateCurrentSlide"
        >
        </a-slide-pagination>
    </div>
</template>

<script>
import ASlidePagination from './ASlidePagination.vue';
import ASlidenext from './ASlidenext.vue';
import ASlideprev from './ASlideprev.vue';
import ASliderItem from './ASliderItem.vue';

    export default {
        name:'a-slider',
        props:{
            slideOne_data:{
                type: Array,
                default: () => [],
            },
            sellItems:{
                type: Array,
                default: () => [],
            },
            sliderPerView:{
                type: Number,
            },
            sliderMargin:{
                type:Number,
                default:0,
            },
            interval:{
                type:Number,
                default:0,
            }
        },
        data(){
            return{
                
                currentSlide:0,
                slideWidth: Number,
                slideInterval:null,
            }
        },
        components:{
            'a-slidenext': ASlidenext,
            'a-slideprev':ASlideprev,
            ASliderItem,
            ASlidePagination,
        },
        methods:{
            nextSlide(){
                if(this.currentSlide >= this.pageCount - 1){
                    this.currentSlide = 0;
                }else{
                    this.currentSlide++
                }
                clearInterval(this.slideInterval)
                this.slideAutoplay()
              
               
            },
            prevSlide(){
                if(this.currentSlide <= 0){
                    this.currentSlide = this.pageCount - 1;
                }else{
                    this.currentSlide--
                }
                clearInterval(this.slideInterval)
                this.slideAutoplay()
              
            },
            slideAutoplay(){
                    if(this.interval > 0){
                    let vm = this;
                    this.slideInterval = setInterval(()=>{
                        vm.nextSlide()
                    },vm.interval) 
                }
            },
         
            updateCurrentSlide(index){
                this.currentSlide = index
            }
        },
        created(){
            window.onresize = this.updateWidthSlide
        },
        mounted(){
            this.slideAutoplay()
            

            
          
           
        },
        computed:{
            pageCount(){
                if(this.slideOne_data.length > 0){
                    return Math.ceil(this.slideOne_data.length - this.sliderPerView + 1)
                }else{
                    return Math.ceil(this.sellItems.length - this.sliderPerView + 1)
                }
                
            },
            slideWithMargin(){
                return 100 /this.sliderPerView;
            },
            
           
        },
        
        
    }
</script>

<style scoped>


.slider__container{
    position: relative;
    max-width: 100%;

    
}

.slider__items{
    display: flex;
    overflow: hidden;

    margin-bottom: 20px;
  
   
}

.slider__sellitems{
    display: flex;
    overflow: hidden;
    margin-bottom: 20px;
}

.slider__buttons{
    display: flex;
    justify-content: space-between;
    position: absolute;
    top: 45%;
    width: 100%;
  
}



.slider-news .slider__buttons{
    top: 35%;
    padding-right:15px;
}

.slider__sellitems .slider__buttons{
    top: 30%;
}
.slider__pagination{
    display: flex;
    justify-content: center;
}





</style>