<template>

    <section class="cart-section" >
        <a-container>
            <ul class="breadcrumb golos-r">
                <li>
                    <router-link to="/homepage"></router-link>
                </li>
                <span>/</span>
                <li>
                    <div> </div>
                </li>
                <span>/</span>
                <li>
                    <router-link to="/cart"></router-link>
                </li>
            </ul>
            <h2 class="cart__title golos-sb"></h2>
           <div class="cart-inner">
            <div class="cart-top">
                <div class="cart-shopping"  v-if="cartProducts.length > 0">
                    <ul class="cart-shopping-items-m" v-if="device === 'mobile'">
                        <li class="cart-shopping-item-m" v-for="item of cartProducts" :key="item.id">
                            <div class="cart-shopping-item__descrition-m golos-m">
                                <div class="cart-shopping-item__img">
                                    <img :src="require('../assets/sellitems/' + item.img)" alt="itemInCart">
                                </div>
                                <div class="cart-shopping-item__descrition-column">
                                    <div class="cart-shopping-item__desc">
                                        <p>{{item.desc}}</p>
                                        <p>{{item.katNum}}</p>
                                    </div>
                                    <div class="cart-shopping-item__artikul-m golos-m">
                                        <p>: 09.1890</p>
                                        <p>-: 4603743188415</p>
                                        <p >:<router-link class="mark" to="/autoelektrika"></router-link></p>
                                    </div>
                                </div>
                            </div>
                           
                            <div class="cart-shopping-item__prices-m">
                                <div class="cart-shopping-item__fav-remove-m">
                                    <div ref="addFav" class="add-fav" :class="{added: item.added}" @click="addToFav(item.id)"></div>
                                    <div class="remove-product" @click="deleteProduct(item.id)"></div>
                                </div>
                                <div class="cart-shopping-item__price">
                                    <p class="golos-m">  1 .</p>
                                    <p class="golos-b">{{item.priceNew}}</p>
                                </div>
                                <div class="cart-shopping-item__counts-m">
                                    <div class="cart-shopping-item__add" @click="addToCart(item.id)">
                                        <div class="item-add"></div>
                                    </div>
                                    <div class="cart-shopping-item__input">
                                        <input type="number"
                                        v-removezero
                                        class="golos-m"
                                        :value="item.count"
                                        :max="item.lot"
                                    
                                        @change="updateItemCount($event.target.value === '' || $event.target.value === null ? 0 : $event.target.value, item)"
                                    >
                                    </div>
                                    <div class="cart-shopping-item__remove" @click="removeFromCart(item.id)">
                                        <div class="item-remove"></div>
                                    </div>
                                </div>
                            </div>
                            <div class="cart-shopping-item__final-price-m">
                                <p class="golos-m"></p>
                                <p class="golos-b">{{ Math.round(item.priceNew * item.count) }}</p>
                             
                            </div>
                            
                            
                        </li>
                        
                       
                        <div class="show-more" v-if="this.$store.state.cartProducts.length > limitCart"  @click="incrementLimit">
                            <button  class="golos-b"> </button>
                        </div>      
                    </ul>
                    <ul class="cart-shopping-items" v-else>
                        <li class="cart-shopping-item" v-for="item of cartProducts" :key="item.id">
                            <div class="cart-shopping-item__descrition golos-m">
                                <div class="cart-shopping-item__img">
                                    <img :src="require('../assets/sellitems/' + item.img)" alt="itemInCart">
                                </div>
                                <div class="cart-shopping-item__desc">
                                    <p>{{item.desc}}</p>
                                    <p>{{item.katNum}}</p>
                                </div>
                            </div>
                            <div class="cart-shopping-item__artikul golos-m">
                                <p>: 09.1890</p>
                                <p>-: 4603743188415</p>
                                <p >:<router-link class="mark" to="/autoelektrika"></router-link></p>
                            </div>
                            <div class="cart-shopping-item__prices">
                                <div class="cart-shopping-item__price">
                                    <p class="golos-m">  1 .</p>
                                    <p class="golos-b">{{item.priceNew}}</p>
                                </div>
                                <div class="cart-shopping-item__counts">
                                    <div class="cart-shopping-item__add" @click="addToCart(item.id)">
                                        <div class="item-add"></div>
                                    </div>
                                    <div class="cart-shopping-item__input">
                                        <input type="number"
                                        v-removezero
                                        class="golos-m"
                                        :value="item.count"
                                        :max="item.lot"
                                    
                                        @change="updateItemCount($event.target.value === '' || $event.target.value === null ? 0 : $event.target.value, item)"
                                    >
                                    </div>
                                    <div class="cart-shopping-item__remove" @click="removeFromCart(item.id)">
                                        <div class="item-remove"></div>
                                    </div>
                                </div>
                                <div class="cart-shopping-item__final-price">
                                    <p class="golos-m"></p>
                                    <p class="golos-b">{{ Math.round(item.priceNew * item.count) }}</p>
                                </div>
                            </div>
                            <div class="cart-shopping-item__fav-remove">
                                <div class="add-fav" :class="{added: item.added}" @click="addToFav(item.id)"></div>
                                <div class="remove-product" @click="deleteProduct(item.id)"></div>
                            </div>
                        </li>  
                        
                        <div class="show-more" v-if="this.$store.state.cartProducts.length > limitCart"  @click="incrementLimit">
                            <button class="golos-b"> </button>
                        </div>      
                    </ul> 
                    <div class="cart-shopping-info">
                        <div class="cart-shopping-info__total golos-b">
                           <div class="total">
                                <p>: </p>
                                <p>{{Math.round(getTotalPrice)}}</p>
                           </div>
                            <div class="total__other">
                                <div class="total__count golos-m">
                                    <p></p>
                                    <p>{{getTotalCount}}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div v-else class="cart-shopping-noitem golos-b">
                    <p> </p> 
                 </div>
            </div>
            <div class="cart-bottom">
                <div class="cart-order">
                    <h2 class="cart-order__title golos-b"> </h2>
                    <hr>
                    <div class="cart-order-inner">
                        <div class="choose-org">
                            <h3 class="choose-org__title golos-b"> </h3>
                            <div class="choose-org__content">
                                <a-cardgenerate
                                    :labelsArr="labels"
                                    :maxLength="30"
                                    :label="' '"
                                ></a-cardgenerate>
                            </div>
                        </div>
                    </div>
                    <hr>
                    <div class="cart-delivery-type">
                        <h3 class="cart-delivery-type__title golos-b">  </h3>
                        <div class="cart-delivery-type__content">
                            <a-tabs :tabs="tabsArr" class="delivery-tabs">
                                <template v-slot:DelovyeLines>
                                    <iframe
                                        src="https://widgets.dellin.ru/calculator?derival_to_door=off&arrival_to_door=on&disabled_calculation=on&insurance=0&package=1"
                                        width="332"
                                        height="390"
                                        scrolling="no"
                                        frameborder="0">
                                    </iframe>
                                </template>
                                <template v-slot:Baykal>
                                    <iframe src="https://api.baikalsr.ru/widget/calculator?" 
                                        width="310" 
                                        height="525" 
                                        scrolling="no" 
                                        frameborder="0">
                                    </iframe>
                                </template>
                            </a-tabs>
                        </div>
                    </div>
                    <hr>
                    <div class="choose-adress">
                        <h3 class="choose-adress__title golos-b"> </h3>
                        <div class="choose-adress__content">
                            <a-cardgenerate
                                :labelsArr="[' ']"
                                :maxLength="60"
                                :label="' '"
                            ></a-cardgenerate>
                        </div>
                     </div>
                     <hr>
                     <div class="comment">
                        <div class="comment-input">
                            <a-textarea
                            :label="'  '"
                            :id="'order'"
                            :maxlength="400"
                            :height="134"
                            v-model="textValue"
                            ></a-textarea>
                        </div>
                     </div>
                </div>
            </div>
              
           </div>
    

    
    
    
    
         

        </a-container>
    </section>
</template>

<script>
    export default {
        name:'a-cartpage',
        data(){
            return{
                labels:[' '],
                cards:[],
                tabsArr:[
                    {title:'DelovyeLines', img:'DelovyeLines.svg', name:'DelovyeLines' },
                    {title:'Baykal', img:'Baykal.svg', name:'Baykal' },
                ],
                textValue:'',
            }
        },
        computed:{
            limitCart(){
                return this.$store.state.limitCart
            },
            cartProducts(){
                return this.$store.state.cartProducts.slice(0, this.limitCart)
            },
            getTotalPrice(){
                let total = 0;
                for ( let item of this.cartProducts){
                    total += item.count * item.priceNew
                    // total = total + item.count * item.priceNew
                }

                return total
            },
            getTotalCount(){
                let count = 0;
                for ( let item of this.cartProducts){
                    count += item.count
                    // total = total + item.count
                }

                return count
            },
          
            
        },
        methods:{
            addToFav(id){
                this.$store.commit('addToFav', id)
            },
            addToCart(id){
                this.$store.commit('addToCart', id)
            },
            removeFromCart(id){
                this.$store.commit('removeFromCart', id)
            },
            deleteProduct(id){
                this.$store.commit('deleteProduct', id)
            },
            updateItemCount(newCount, item){
                this.$store.commit('updateItemCount', {
                    id:item.id,
                    count:parseInt(newCount),
                    lot:item.lot
                })
            },
            incrementLimit(){
                this.$store.state.limitCart += 5;
            },
           
        },
       
        
    }
</script>

<style scoped>
