  max-width: 1050px;
  display: flex;
  flex-direction: column;
  row-gap: 10px;
}

.cart-shopping-items, .cart-shopping-noitem{
    position: relative;
    flex: 0 1 80%;
    min-height: 300px;
   
}

.cart-shopping-noitem{
  border: 2px solid #C2C4D2;
  border-radius: 20px;
  display: flex;
