<template>
    <div class="catalog">
        <button class="catalog-btn_mobile golos-m" v-if="device === 'mobile'" @click="showCatalog">
            <div class="catalog-btn__icon">
                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M8.01588 1.5H2.48412C1.94061 1.5 1.5 1.94061 1.5 2.48412V8.01588C1.5 8.55939 1.94061 9 2.48412 9H8.01588C8.55939 9 9 8.55939 9 8.01588V2.48412C9 1.94061 8.55939 1.5 8.01588 1.5Z" stroke="#6E7191" stroke-width="2"/>
                    <path d="M21.5159 1.5H15.9841C15.4406 1.5 15 1.94061 15 2.48412V8.01588C15 8.55939 15.4406 9 15.9841 9H21.5159C22.0594 9 22.5 8.55939 22.5 8.01588V2.48412C22.5 1.94061 22.0594 1.5 21.5159 1.5Z" stroke="#6E7191" stroke-width="2"/>
                    <path d="M8.01588 15H2.48412C1.94061 15 1.5 15.4406 1.5 15.9841V21.5159C1.5 22.0594 1.94061 22.5 2.48412 22.5H8.01588C8.55939 22.5 9 22.0594 9 21.5159V15.9841C9 15.4406 8.55939 15 8.01588 15Z" stroke="#6E7191" stroke-width="2"/>
                    <path d="M21.5159 15H15.9841C15.4406 15 15 15.4406 15 15.9841V21.5159C15 22.0594 15.4406 22.5 15.9841 22.5H21.5159C22.0594 22.5 22.5 22.0594 22.5 21.5159V15.9841C22.5 15.4406 22.0594 15 21.5159 15Z" stroke="#6E7191" stroke-width="2"/>
                    </svg>                   
            </div>
            <div class="catalog-btn__label"></div>                 
        </button>
        <button class="catalog-btn golos-b" v-else @click="showCatalog">
            <div class="catalog-btn-icon">
                <span></span>
            </div>
            <p class="catalog-btn__label" v-if="device === 'desktop'"></p>
        </button>
        


  
            <div class="catalog__inner" ref="catalog__inner" :class="{active : isActive}" @click="hideCatalog">
                <div v-show="isActive" class="overlay-catalog"></div>
                <nav class="catalog-menu-m" @click.stop v-if="device === 'mobile'" >
                    <div class="catalog-row">
                        <div class="catalog-menu__close" @click="hideCatalog">
                        </div>
                        <div class="catalog-menu__title menu__title"></div>         
                    </div>
                    <ul class="catalog-menu__lists" >
                        <li class="catalog-menu__list-label catalog-label  golos-m" :class="{open : menuItemOne.open}" v-for="(menuItemOne, i) in menuItemsOne" :key="i" @mouseenter="hoverSvg = i" @mouseleave="hoverSvg = null" >
                            <router-link @click="hideCatalog" :to="menuItemOne.path">
                                <div class="catalog-menu__list-icon">
                                    <img :src="hoverSvg === i ? require('@/assets/catalogIcons/' + menuItemOne.activeIcon) : require('@/assets/catalogIcons/' + menuItemOne.icon) " :alt="menuItemOne.name">
                                </div>
                                <p>{{menuItemOne.name}}</p>
                            </router-link>
                                <div class="nextCatalog" @click="menuItemOne.open = true"></div>
                            <ul class="catalog-submenu " v-if="menuItemOne.menuItemsTwo" @click.stop>
                                <div class="catalog-row">
                                    <div class="catalog__back" @click="menuItemOne.open = false">
                                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                            <path d="M10.1308 4.64612L3.07202 12L10.1308 19.3539" stroke="#6E7191" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                            <path d="M4.40332 12.0324L20.9308 12.0324" stroke="#6E7191" stroke-width="2" stroke-linecap="round"/>
                                        </svg>  
                                    </div>
                                    <p class="catalog-submenu__title menu__title" >{{menuItemOne.name}}</p>
                                </div>
                                <li class="catalog-submenu__list-label catalog-label  golos-m" :class="{open : menuItemTwo.open}" v-for="(menuItemTwo, s) in menuItemOne.menuItemsTwo" :key="s"  @click="menuItemTwo.open = true">
                                    <a href="#">{{ menuItemTwo.name }}</a>
                                    <ul class="catalog-sub-submenu " v-if=" menuItemTwo.menuItemsThree" @click.stop>
                                        <div class="catalog-row">
                                            <div class="catalog__back" @click="menuItemTwo.open = false">
                                                <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                                    <path d="M10.1308 4.64612L3.07202 12L10.1308 19.3539" stroke="#6E7191" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                                                    <path d="M4.40332 12.0324L20.9308 12.0324" stroke="#6E7191" stroke-width="2" stroke-linecap="round"/>
                                                </svg>                                                
                                            </div>
                                            <p class="catalog-sub-submenu__title menu__title">{{menuItemTwo.name}}</p>
                                        </div>
                                        <li class="catalog-sub-submenu__list-label catalog-label  golos-m" v-for="(menuItemThree, t) in menuItemTwo.menuItemsThree" :key="t" >
                                           <a href="#"> {{ menuItemThree.name }}</a>
                                        </li>
                                    </ul>
                                </li>   
                            </ul>
                            
                        </li>
                    </ul>
                    <div class="catalog__banner">
                        <img src="../../assets/Banner.png" alt="banner">
                    </div>
                </nav>
                <nav class="catalog-menu" @click.stop v-else >
                    <ul class="catalog-menu__lists" >
                        <li class="catalog-menu__list-label catalog-label  golos-m" :class="{open : menuItemOne.open}" v-for="(menuItemOne, i) in menuItemsOne" :key="i" @mouseenter="hoverSvg = i" @mouseleave="hoverSvg = null">
                            <router-link @click="hideCatalog" :to="menuItemOne.path">
                                <div class="catalog-menu__list-icon">
                                    <img :src="hoverSvg === i ? require('@/assets/catalogIcons/' + menuItemOne.activeIcon) : require('@/assets/catalogIcons/' + menuItemOne.icon) " alt="">
                                </div>
                                <p>{{menuItemOne.name}}</p>
                            </router-link>
                            <ul class="catalog-submenu" v-if="menuItemOne.menuItemsTwo">
                                <p class="catalog-submenu__title menu__title" >{{menuItemOne.name}}</p>
                                <li class="catalog-submenu__list-label catalog-label  golos-m" :class="{open : menuItemTwo.open}" v-for="(menuItemTwo, s) in menuItemOne.menuItemsTwo" :key="s">
                                    <a href="#">{{ menuItemTwo.name }}</a>
                                    <ul class="catalog-sub-submenu" v-if=" menuItemTwo.menuItemsThree">
                                        <p class="catalog-sub-submenu__title menu__title">{{menuItemTwo.name}}</p>
                                        <li class="catalog-sub-submenu__list-label catalog-label  golos-m" v-for="(menuItemThree, t) in menuItemTwo.menuItemsThree" :key="t" >
                                           <a href="#"> {{ menuItemThree.name }}</a>
                                        </li>
                                    </ul>
                                </li>   
                            </ul>
                            
                        </li>
                    </ul>
                    <div class="catalog__banner">
                        <img src="../../assets/Banner.png" alt="banner">
                    </div>
                </nav>
               
            </div>
   
    </div>
</template>

<script>

    export default {
        name:'a-catalog',
        mounted(){
            this.isActive = false
            this.$emit('catalogItems', this.menuItemsOne)
        },
        data(){
            return{
                isActive:false,
                hoverSvg:null,
                menuItemsOne:[
                    {name:'',
                    icon:'menu.svg',
                    activeIcon:'Activemenu.svg',
                    path:'/autoelektrika',
                    open:false,
                        menuItemsTwo:[
                            {name:'  ',
                            path:'/autoelektrika/starters',
                            open:false,
                                menuItemsThree:[
                                    {name:'', },
                                    {name:'   ', },
                                    {name:'   ', },
                                    {name:' ', },
                                    {name:' ', },
                                    {name:' ', },
                                    {name:'  ', },
                                    {name:' ', },
                                    {parent:'  '},
                                    

                                ]
                            },
                            {name:'',
                            path:'/autoelektrika/generators',
                            },
                            {name:'', 
                            path:'/autoelektrika/svetotechnika',
                            },
                            {name:' ',
                            path:'/autoelektrika/elektrodvigateli',
                            },
                            {name:'  ', open:false,
                            path:'/autoelektrika/stekla',
                                    menuItemsThree:[
                                            {name:''},
                                            {name:'   ',},
                                            {name:'   ', },
                                            {name:' ', },
                                            {name:' ', },
                                            {name:' '},
                                            {name:'  ',},
                                            {name:' ', },
                                            {parent:'  '}
                                            

                                        ]
                            },
                            {name:'', 
                            path:'/autoelektrika/karburator'
                            },
                            {name:'',
                            path:'/autoelektrika/termostat'
                            },
                            {parent:''}
                            
                        ]
                    },
                    {name:'Aimpuls',
                    path:'/aimpuls',
                     icon:'menu-1.svg',
                     activeIcon:'Activemenu-1.svg', },
                    {name:'',
                    path:'/novinki',
                    icon:'menu-2.svg',
                    activeIcon:'Activemenu-2.svg',
                    },
                    {name:'  ',
                    path:'/autozapchasti',
                    icon:'menu-3.svg',
                    activeIcon:'Activemenu-3.svg', },
                    {name:'',
                    path:'/komplekt',
                    icon:'menu-4.svg',
                    activeIcon:'Activemenu-4.svg',},
                    
                ],

            }
        },
   
        methods:{
            
            mouseEnter(item){
                this.hoverSvg = item
            },
            mouseLeave(){
                this.hoverSvg = null
            },
            showCatalog(){
                this.isActive = !this.isActive
                if (this.isActive){
                    document.body.classList.add('_lock')
                }else{
                    document.body.classList.remove('_lock')
                }
                this.$emit('showOverlay')
                this.menuItemsOne.forEach(item=> {
                    item.open = false
                })
               
            },
            hideCatalog(){
                this.isActive = false
                this.$emit('hideOverlay')
                this.menuItemsOne.forEach(item=> {
                    item.open = false
                })
                document.body.classList.remove('_lock')
            },
    
           
           
          
          
          
        }
    }
</script>

<style scoped>

    .nextCatalog{
        position: absolute;
        right: 0;
        top: 0;
        height: 100%;
        width: 10%;
        margin-left: auto;
    }
    .overlay-catalog{
        width: 50vw;
        right:100%;
        top: 0;
        position: absolute;
        background: white;
        min-height: 100vh;
    }
    .catalog__back, .catalog-menu__close{
        cursor: pointer;
        position: relative;
        width: 24px;
        height: 24px;
    }

    .catalog-menu__close::before,
    .catalog-menu__close::after{
        content: '';
        position: absolute;
        width: 100%;
        height: 3px;
        background-color: #6E7191;
        transform-origin: center;


    }

    .catalog-menu__close::before{
        top: 10px;
        transform: rotate(45deg);
    }
    .catalog-menu__close::after{
        bottom: 11px;
        transform: rotate(-45deg);
    }

    .catalog-btn{
        background: #F56E1E;
        border-radius: 12px;
        padding: 13px 38px;
        display: flex;
        align-items: center;
        gap: 10px;
        color: white;
        transition: all 0.5s ease;
   
    }
    .catalog-btn:hover{
        background: #DE5703;
    }

    .catalog-btn-icon{
        width: 22px;
        height: 23px;
        position: relative;
     
    }
    .catalog-btn-icon::before,
    .catalog-btn-icon::after{
        content: '';
        width: 100%;
        height: 2px;
        position: absolute;
        background-color: white;
        left: 0;
        transition: all 0.2s ease;
    }

    .catalog-btn-icon::before{
        top: 0;
    }
    .catalog-btn-icon::after{
        bottom: 0;
    }

    .catalog-btn-icon span{
        position: absolute;
        width: 100%;
        height: 2px;
        background-color: white;
        top: 50%;
        left: 0;
        transform: translateY(-60%);
        transition: all 0.2s ease;
    }


    .catalog:has(.catalog__inner.active) .catalog-btn-icon::before{
        top: 45%;
        background-color: #F56E1E;
        transform: rotate(40deg);
    }
    .catalog:has(.catalog__inner.active) .catalog-btn-icon::after{
        bottom: 45%;
        background-color: #F56E1E;
        transform: rotate(-40deg);
    }

    .catalog:has(.catalog__inner.active) .catalog-btn-icon span{
        opacity: 0;

    }
    .catalog:has(.catalog__inner.active) .catalog-btn{
        background:   #FCE1D2;
        color:#F56E1E ;
    }
  
    .catalog:has(.catalog__inner.active) .catalog-btn__icon svg path{
        stroke: #F56E1E;
    }

    .catalog:has(.catalog__inner.active) .catalog-btn__label{
        color: #F56E1E;
    }
    .catalog-btn_mobile:hover .catalog-btn__icon svg path {
        stroke: #F56E1E;
    }

    .catalog-btn_mobile:hover .catalog-btn__label{
        color: #F56E1E;
    }
    .catalog-btn_mobile .catalog-btn__icon svg path{
        transition: all 0.2s ease;
    }

    .catalog-btn_mobile .catalog-btn__label{
        transition: all 0.2s ease;
    }


 


    .catalog__inner{
        left: -100%;
        width: 0;
        visibility: hidden;
        opacity: 0;
        position: absolute;
        min-height: 100vh;
        transition: all 0.5s ease;
        z-index: 90;
        top: 80px;
    }

    .catalog__inner.active{
        width: 100%;
        left: 0;
        visibility: visible;
        opacity: 1;
      
    }
    .catalog-row{
        display: flex;
        font-size: 16px;
        color:  #6E7191;
        padding: 0 20px;
        
    }


    .catalog-menu{
        position: absolute;
        background: #fff;
        max-width: 360px;
    }

    .catalog-menu, 
    .catalog-menu ul{
        background-color: #fff;
        min-height: 100%;
   
    }

    .catalog-menu__lists{
        padding-top: 30px;
    }
 
   .catalog-label{
      padding: 0 30px;
    }
    .catalog-label:hover > a {
        color: white;
        background-color: #F56E1E;
        border-radius: 12px;
    }
 
    .catalog-submenu .catalog-label:hover > a,
    .catalog-sub-submenu .catalog-label:hover > a{
        background: transparent;
        color: #DE5703;
        
    }

    .catalog-menu .catalog-menu__list-label:hover .catalog-submenu{
        visibility: visible;
        opacity: 1;
        width: 100%;
        z-index: 1;
     
    
    }
   
    .catalog-menu .catalog-submenu__list-label:hover .catalog-sub-submenu{
        visibility: visible;
        opacity: 1;
        width: 100%;
        z-index: 2;
        left: 100%;
       
        
    }
    .catalog-label a{
        position: relative;
        display: flex;
        align-items: center;
        gap: 12px;
        padding: 8px 12px;
        border-radius: 12px;
        cursor: pointer;
       
      
    }

 
   

    

  



    .catalog-label:has(ul) a::after{
        content: '';
        position: absolute;
        width: 20px;
        height: 20px;
        top: 30%;
        right: 0;
        background: url('../../assets/catalogIcons/arrows/arrowMenu.svg') no-repeat;
    }

    .catalog-label:hover a::after{
        background: url('../../assets/catalogIcons/arrows/arrowMenuActive.svg') no-repeat;
    }
    .catalog-submenu .catalog-label:has(ul) a::after{
        background: url('../../assets/catalogIcons/arrows/arrowSubMenu.svg') no-repeat;
    }

    .catalog-submenu .catalog-label:has(ul):hover a::after,
    .catalog-sub-submenu .catalog-label:has(ul):hover a::after{
        background: url('../../assets/catalogIcons/arrows/arrowSubMenuActive.svg') no-repeat;
    }

    .catalog-submenu,
    .catalog-sub-submenu{
        display: flex;
        flex-direction: column;
        position: absolute;
        gap: 16px;
        visibility: hidden;
        opacity: 0;
        transition: opacity 0.5s ease, left 0.5s ease;
        left: 100%;
        width: 0;
        top: 0;
        padding: 10px 0;
        max-width: 300px;
     
 
       
    }


    .catalog-menu-m .catalog-submenu,
    .catalog-menu-m .catalog-sub-submenu{
        transition:all 0.5s ease;
    }

    .catalog-sub-submenu .catalog-label a::after{
        content: none;
    }

    .menu__title{
        padding-left: 30px;
        color: #F56E1E;
    }

    .catalog__banner{
        margin-top: 30px;
        padding: 0 30px;
    }

    .catalog__banner img{
        width: 100%;
    }

    .catalog-btn._mobile:hover .catalog-btn__label{
    
        color: #F56E1E;
    }
    .catalog-btn._mobile:hover path{
        stroke: #F56E1E;
    }

   .icon path{
    fill: #F56E1E;
   }


    






    @media (max-width:1024px) {
        .catalog-btn{
            padding: 12px 16px;
        }
        .catalog-menu,
        .catalog-submenu,
        .catalog-sub-submenu{
            max-width: 250px;
        }
        .catalog-label{
            padding: 0 20px;
        }
    }

    @media (max-width:768px) {
     
     
        .catalog-menu-m{
            padding-top: 26px;
            min-height: 100vh;
            background-color: white;
        }
        .catalog-menu-m ul{
            padding-top: 26px;
            min-height: 100%;
            background-color: white;
         }
        .catalog-menu__list-label.open .catalog-submenu,
        .catalog-submenu__list-label.open .catalog-sub-submenu{
            left: 0;
            max-width: none;
            width: 100%;
            visibility: visible;
            opacity: 1;
        }
        .catalog-menu__list-label.open .catalog-submenu{
            z-index: 2;
        }
        .catalog-menu__list-label.open .catalog-sub-submenu{
            z-index: 3;
        }
        .menu__title{
            font-weight: 600;
            margin: 0 auto;
            color: #6E7191;
            max-width: 157px;
            padding: 0;
        }

      
    
       .catalog__inner{
        position: fixed;
        z-index: -1;
        transform: none;
        top: 0;
       }

       .catalog-menu{
      
        width: 100%;
        max-width: none;
       }
       

       .catalog__banner{
        max-width: 345px;
       }
     
    }

    @media (max-width:399px){
        .catalog-label p{
            max-width: 160px
        }
        .catalog-btn__icon svg{
            width: 18px;
            height: 18px;
           }
           .catalog-btn__label{
            font-size: 9px;
           }
    }
</style>