<template>
    <div class="forget" v-if="forget" @click="hideSign">
        <div class="forget__container"  >
            <div class="forget__inner" @click.stop>
                <div class="forget__row">
                    <div class="forget__back" @click="hideForget">
                        <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M10.1308 4.64612L3.07202 12L10.1308 19.3539" stroke="#6E7191" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                            <path d="M4.40332 12.0324L20.9308 12.0324" stroke="#6E7191" stroke-width="2" stroke-linecap="round"/>
                        </svg>
                    </div>
                    <div class="forget__close" @click="hideSign">
                        <div></div>
                    </div>
                </div>
                <div class="forget__content">
                    <h2 class="forget__title golos-sb"> </h2>
                    <form >
                        <div class="forget-input__email golos-b">
                            <input class="input__email" type="email" required>
                            <label for="email" class="placeholder"> .</label>
                        </div>
                    </form>
                    <div class="forget__button golos-b">
                        <a-button class="forget__submit"></a-button>
                    </div>
                </div>    
            </div>
        </div>
    </div>
    
</template>

<script>
   
    export default {
        name: 'a-forget' ,
        props:{
            forget:{
                type: Boolean,
                default: false,
            }
        },
        data() {
            return {
                
            }
        },
        methods:{
            hideForget(){
                this.$emit('update:forget', false)
            },
            hideSign(){
                this.$emit('update:forget', false)
                this.$emit('runHideSign')
            }
        }
    }
</script>

<style scoped>
    .placeholder {
        position: absolute;
        top: 15px;
        left: 10px;
        pointer-events: none;
        transition: 0.2s ease all;
    }
    .forget{
        
        position: fixed;
        right: 0;
        left: 0;
        top: 0;
        bottom: 0;
        z-index: 1600;
    }

    .forget__container{
        display: flex;
        justify-content: end;
    }

    .forget__inner{
        width: 100%;
        max-width: 560px;
        position: relative;
        display: flex;
        flex-direction: column;
        row-gap: 60px;
        min-height: 100vh;
        background: white;
        padding: 40px;

    }

    .forget__row{
        display: flex;
        justify-content: space-between;
    }
    .forget__close {
        display: flex;
        justify-content: end;
    }
    .forget__close div{
        cursor: pointer;
        position: relative;
        background: #d6d6d6;
        border-radius: 50%;
        width: 30px;
        height: 30px;
    }
    
    .forget__close div::before,
    .forget__close div::after{
        content: '';
        position: absolute;
        top: 14px;
        left: 5px;
        width: 70%;
        height: 3px;
        background-color: #A0A3BC;
        transform-origin: center ;
        transform: rotate(45deg);
    }
    
    .forget__close div::after{
        transform: rotate(135deg);
    }

    .forget__content{
        display: flex;
        flex-direction: column;
        gap: 40px;
    }

    .forget__title{
        text-align: center;
    }

    .forget-input__email{
        position: relative;
        border: 1px solid #A0A3BC;
        border-radius: 12px;
        font-weight: 500;
        padding: 16px 12px;
        display: flex;
    }

    .input__email{
        width: 100%;
    }
    .forget__button{
        text-align: center;
    }

    .forget__submit:hover {
        background: #e4e4e4;
        color:#F56E1E ;
    }
    .forget__submit{
        background: #F56E1E;
        color: white;
        max-width: 210px;
    }


    .input__email:focus + .placeholder,
    .input__email:not(:focus):valid + .placeholder {
      top: 0;
      font-size: 12px;
      color: #888;
  }
  
</style>