<template>
    <div class="select-wrapper" :class="[className, {opened:showDrop}]" @click="showDrops(className)" v-click-outside="hide" >
        <div class="select-content" @click.stop>
            <div class="select-default golos-m">{{defaultItem.name}}</div>
            <span class="select-placeholder golos-r" v-if="Object.keys(defaultItem).length === 0">{{placeholder}}</span>
            <div class="select-options-wrapper" v-if="showDrop===className">
                <ul class="select-options golos-m">
                    <li class="select-option" v-for="item, i in items" :key="i" @click="selectCounty(item); hide(); $emit('selectCountry', defaultItem.name)">
                        <p class="select-option-item">{{item.name}}</p>
                    </li>
                </ul>
            </div> 
        </div>
    </div>
</template>

<script>
    export default {
        name:'a-select',
        props:{
            className:String,
            placeholder:String,
            items:Array,
        },
        data(){
            return{
                defaultItem:{},
                showDrop:'',
            }
        },
        methods:{
            showDrops(drop){
                this.showDrop = (this.showDrop === drop) ? '' : drop;
            },
            hide(){
                this.showDrop='';
            },
            selectCounty(item){
                this.defaultItem = item
            },
        }
    }
</script>

<style scoped>

.select-wrapper{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 12px;
    border-radius: 12px;
    border: 1px solid #A0A3BC;
    background: #FFF;
    min-width: 305px;
    cursor: pointer;
    flex: 0 1 47%;
}

.opened{
    border-color:#F56E1E;
}


.select-default{
    font-size: 16px;
}

.select-options-wrapper{
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 100%;
    left: 0;
    padding-top: 5px;
    z-index: 999;
}

.select-options{
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 12px;
    border-radius: 12px;
    background: #FFF;
    box-shadow: 0 0 2px 1px #A0A3BC;
}

.select-placeholder{
    font-size: 16px;
    color: #A0A3BC;
}

.select-option{
    cursor: pointer;
}

.select-option:hover{
    color: #c0c3df;
}




@media (max-width:399px){
    .select-wrapper{
        min-width: 180px;
    }
}


</style>