<template>
    <label class="golos-b" :for="id">{{label}}</label>
    <div class="textfield" :style="{maxWidth: maxWidth+'px', height: height+'px', width:width+'px'}">
        <textarea class="textfield__textarea golos-m" 
            :name="name" 
            :id="id" 
            :maxlength="maxlength" 
            :value="modelValue"
            @input="$emit('update:modelValue', $event.target.value)"></textarea>
        <div class="length golos-r">
            {{ modelValue.length }}/{{ maxlength }}
        </div>
    </div>
</template>

<script>
    export default {
        name:'a-textarea',
        props:{
            name:String,
            id:[String, Array],
            maxlength:Number,
            label:String,
            maxWidth:Number,
            width:Number,
            height:Number,
            modelValue:[String, Number]
        },
        data(){
            return{
                lenghtNow:0,
            }
        }
    }
</script>

<style scoped>

.textfield{
    position: relative;
    border-radius: 12px;
  

}

.textfield__textarea{
    border: 2px solid #A0A3BC;
    padding: 14px;
    border-radius: 12px;
    resize: none;
    outline: none;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.length{
    position: absolute;
    right: 13px;
    bottom: 7px;
}



</style>