<template>
    <div class="search-mobile" v-if=" device === 'mobile'" @click="showSearch">
        <div class="search-mobile__icon">
            <svg width="25" height="25" viewBox="0 0 25 25" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M10.9755 20.439C12.8472 20.439 14.6768 19.8839 16.2331 18.8441C17.7894 17.8042 19.0023 16.3262 19.7186 14.597C20.4349 12.8678 20.6223 10.965 20.2571 9.12923C19.892 7.29349 18.9907 5.60726 17.6672 4.28376C16.3437 2.96027 14.6574 2.05896 12.8217 1.69381C10.986 1.32865 9.08317 1.51606 7.35394 2.23233C5.62471 2.9486 4.14671 4.16156 3.10685 5.71783C2.06699 7.27409 1.51196 9.10376 1.51196 10.9755C1.51196 13.4853 2.50901 15.8924 4.28376 17.6672C6.05851 19.4419 8.46559 20.439 10.9755 20.439V20.439Z" stroke="#F56E1E" stroke-width="3" stroke-linecap="round" stroke-linejoin="round"/>
                <path d="M22.5436 22.5436L17.7766 17.7766" stroke="#F56E1E" stroke-width="3" stroke-linecap="round" stroke-linejoin="round"/>
            </svg>                
        </div>
    </div>
  <form :class="{active : searchActive}">
    <div class="search"  >
        <div class="search__content">
            <input type="text" name="search" id="search" placeholder=" " class="golos-m" v-model="keywords" @input="filterArr" autocomplete="off">
            <div class="search__close" v-if=" device === 'mobile'" @click="hideSearch">
                <svg width="20" height="20" viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M11.5728 9.97653L16.4085 5.18779L17.3944 4.20188C17.5352 4.06103 17.5352 3.82629 17.3944 3.6385L16.3615 2.60563C16.1737 2.46479 15.939 2.46479 15.7981 2.60563L10.0235 8.42723L4.20188 2.60563C4.06103 2.46479 3.82629 2.46479 3.6385 2.60563L2.60563 3.6385C2.46479 3.82629 2.46479 4.06103 2.60563 4.20188L8.42723 9.97653L2.60563 15.7981C2.46479 15.939 2.46479 16.1737 2.60563 16.3615L3.6385 17.3944C3.82629 17.5352 4.06103 17.5352 4.20188 17.3944L10.0235 11.5728L14.8122 16.4085L15.7981 17.3944C15.939 17.5352 16.1737 17.5352 16.3615 17.3944L17.3944 16.3615C17.5352 16.1737 17.5352 15.939 17.3944 15.7981L11.5728 9.97653Z" fill="#A0A3BC"/>
                    </svg>
                    
            </div>
            <div class="search__icon">
                <img src="../../assets/search/search.svg" alt="search">
            </div>
        </div>
        <div class="search-result">
            <ul class="search-result__inner" v-if="keywords.length > 0">
                <li class="search-result__list golos-m" v-for="result, i in searchItems" :key="i">
                    {{result.name}}
                    <small v-if="result.parent">{{ result.parent }} / {{result.name}}</small>
                </li>
               
            </ul>
        </div>
    </div>
    <div class="input-active-overlay"></div>
 
   
  </form>
</template>

<script>
    export default {
        name:'a-search',
        props:{
            catalogItems:{
                type:Array,
                default:()=>[]

            }
        },
        data(){
            return{
                searchActive: false,
                keywords: '',
                input:null,
                searchItems:[]
            }
        },
        methods: {
            showSearch(){
                this.searchActive = true;
            },
            hideSearch(){
                this.searchActive = false; 
            },
            filterArr(){
                this.searchItems = this.searchResults(this.keywords, this.catalogItems)
            },
            searchResults(key, items, parent = '') {
                let arr = []

                for (const item of items){
                     
                    
                    if(item.name && item.name.toLowerCase().includes(key.toLowerCase())){
                        arr.push({
                            name: item.name,
                            parent: parent,
                        })
                    }

                    if(item.menuItemsTwo){
                        arr = arr.concat(this.searchResults(key, item.menuItemsTwo, item.name)) 
                    }
                    if(item.menuItemsThree){
                        arr = arr.concat(this.searchResults(key, item.menuItemsThree, item.name))
                    }
                }
                return arr
            }

        },
        computed:{
           
        }
    }
</script>

<style scoped>
    .input-active-overlay{
        visibility: hidden;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: rgba(0, 0, 0, 0.3);
        z-index: 500;
    }
    .search{
        position: relative;
        z-index: 9999;
       
      
       
    }
    .search__content{
        border: 1px solid #A0A3BC;
        border-radius: 12px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 17px 12px;
        gap: 10px;
        background-color: white;
    }
    
   .search input{
    position: relative;
    flex: 1 1 100%;
    color: #A0A3BC;
   }

   .search:has(input:focus) + .input-active-overlay{
    visibility: visible;

   }
   .search-result{
    position: absolute;
    width: 100%;
    top: 105%;
   }
   .search-result__inner{
    overflow: auto;
    max-height: 80vh;
    display: flex;
    flex-direction: column;
    gap: 24px;
    border-radius: 12px;
    background: #FFF;
    padding: 18px;
    /* shadow 3 */
    box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.20);
   }
   .search-result__list {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-left: 25px;
   }
   .search-result__list::before{
    content: '';
    position: absolute;
    width: 15px;
    height: 100%;
    left: 0;
    top: 0;
    background: url('../../assets/search/result-icon.svg') no-repeat;
    background-size: contain;
    background-position: center;
   }

   .search-result__list small{
    color:  #A0A3BC;
    font-size: 12px;
    font-weight: 400;
    line-height: 12px; /* 100% */
   }

  

   @media (max-width: 768px){
        form{
            position: fixed;
            max-height: 0;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-color: white;
            opacity: 0;
            visibility: hidden;
            z-index: -999;
            transition: all 0.3s ease;
        }
        form.active{
            min-height: 100vh;
            opacity: 1;
            visibility: visible;
            z-index: 999;
        }
        .search {
            margin: 10px;
            padding: 12px 7px;
        }

        .search__content{
            padding: 16px 12px;
        }
   }
   @media (max-width:299px) {
        .search__content{
            padding: 7px 5px;
        }
        .search__content input{
            max-width: 150px;
        }
    
   }
</style>