<template>
    <div class="region-ds">
       <div class="select-region">
            <div class="select-region__wrap golos-m">
                <div class="default-option" v-if="device ==='desktop' || device === 'tablet'">
                    <div class="option" :class="[selectValue.class, {'open': opVisible}]" >
                        <div><img :src="require('@/assets/region/'+ selectValue.img)" :alt="selectValue.img"></div>
                        <p>{{selectValue.name}}</p>
                        <div class="option__arrow subheader__arrow">
                            <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M3 6L8 11L13 6" stroke="#A0A3BC" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                            </svg>       
                        </div>
                    </div>
                </div>
                <ul class="select-options">
                    <li class="select-option" v-for="region in RegionList" :key="region.id" @click="selectOption(region)">
                        <div class="option" :class="region.class"> 
                            <div><img :src="require('@/assets/region/'+ region.img)" :alt="region.name"></div>
                            <p>{{region.name}}</p>
                        </div>
                    </li>
                </ul>
            </div>
       </div>
    </div>
</template>

<script>
    
    export default {
      name:'a-region',
      data() {
        return {
            opVisible:false,
            RegionList:[
               {id:1, name:'RU', class:'ru', img:'RU.svg'},
               {id:2, name:'', class:'cn', img:'CN.svg'}
                
            ],
            selectValue:{id:1, name:'RU', class:'ru', img:'RU.svg'}
        }
      }, 
      methods:{
        selectOption(region){
            this.selectValue = region
            this.opVisible = false
        }
      },
    }
</script>

<style scoped>
img{
    width: 100%;
}

.region-ds{
    display: inline-block;
    position: relative;
}


.select-region__wrap{
 font-weight: 700;   
}

.option{
    gap: 4px;
    display: flex;
    align-items: center;
    padding: 4px 8px;
    transition: all 0.4s ease;
    border-radius: 8px;
    cursor: pointer;
}

.option svg path{
    transition: all 0.4s ease;
   

}




.option div{
    width: 20px;
    height: 20px;
}

.option img{
    border-radius: 50%;
    border: 2px solid gray;
}

.select-region:hover .default-option .option{
    background:  #F1F1F1;
    color: #F56E1E;
}

.select-region:hover .default-option .option svg path{
    transform: scaleY(-1) translateY(-2px) ;
    transform-origin: center;
    stroke: #F56E1E;
}

.select-option .option:hover{
    color: #F1F1F1;
    background-color: #F56E1E;
}

.select-option .option:hover img{
    border-color: white;
}









@media (min-width: 768px) {

    .select-options{
        margin-top: 5px;
        position: absolute;
        display: flex;
        flex-direction: column;
        gap: 4px;
        opacity: 0;
        visibility: hidden;
        width: 93px;
        transition: all 0.3s ease;
        padding: 4px;
        border-radius: 12px;
        background: #FFF;
        box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.20);
        z-index: 5;
    }
   
    
    .select-region:hover .select-options{
        opacity: 1;
        visibility: visible;
        
    }

 

    
    
    
}


@media(max-width:768px){
    
    .region-ds{
        display: block;
       
    }
    .select-options{
        display: flex;
        gap: 15px;
        position: static;

    }

    .select-option{
        flex: 0 1 50%;
    
    }

    .select-option .option{
        justify-content: center;
        background-color: #F1F1F1;
    }
}

</style>