<template>
    <div class="tabs">
        <div
          v-for="(tab, index) in tabs"
          :key="index"
          class="tab-item"
          :class="{ active: activeTab === index }"
          @click="activeTab = index"
        >
            <div class="before">
                <img :src="activeTab === index  ? require('@/assets/checked.svg') : require('@/assets/uncheck.svg')" alt="check">
            </div>
            <div class="text">
                <img v-if="tab.img" :src="require('@/assets/'+tab.img)" :alt="tab.name">
                <span v-else>{{ tab.title }}</span>
            </div>
        </div>
    </div>
    <div class="tab-content">
        <slot :name="tabs[activeTab].name"></slot>
    </div>
</template>

<script>
    export default {
        name:'a-tabs',
        props:{
            tabs: Array,
        },
        data(){
            return{
                activeTab:0,
            }
        }
    }
</script>

<style scoped>
.tabs{
    display: flex;
    flex-direction: column;
    flex-basis: 40%;
    gap: 10px;
}
.tab-item{
    position: relative;
    padding: 14px 12px;
    border-radius: 12px;
    border: 1px solid #F1F1F1 ;
    background: var(--Gray-05, #F1F1F1);
    display: flex;
    justify-content: center;
    align-items: center;
}

.tab-item.active{
    border: 1px solid #F56E1E;
}

.tab-content{
    position: relative;
}
.before {
    position: absolute;
    left: 12px;
    width: 16px;
    height: 16px;
}
.before img{
    width: 100%;
}

@media (max-width:768px){
    .tab-content{
        display: flex;
        justify-content: center;
    }
}
</style>