<template>
    <a-forget v-model:forget="forgetActive" @runHideSign="hideSign"></a-forget>
    <div class="sign" v-if="show" @click="hideSign">
        <div class="sign__container" >
            <div class="sign__inner" @click.stop>
                <div class="sign__close" >
                    <div @click="hideSign"></div>
                </div>
                <div class="sign__content">
                    <h2 class="sign__title golos-sb">  </h2>
                    <form action="#" class="sign-form">
                        <div class="sign-form__content" >
                            <div class="sign-form__inputs" @click.stop>
                                <div class="sign-form__email golos-b" :class="{error:errMsg===' ' || errMsg==='   '}">
                                    <input class="input__email" type="text" name="email" id="email" v-model.lazy="eMail">
                                    <label for="email" class="placeholder"> .</label>
                                    <div class="checkValid"></div>
                                </div>
                                <div class="sign-form__password golos-b" :class="{error:errMsg===' ' || errMsg==='   '}">
                                    <input class="input__password" type="password" name="password" id="password" v-model.lazy="password">
                                    <label for="password" class="placeholder"></label>
                                    <div class="checkValid"></div>
                                </div>
                                <div class="invalid golos-r" v-if="errMsg">
                                    <small>{{errMsg}}</small>
                                    <small> </small>
                                </div>
                            </div> 
                            <div class="forgotPassword">
                                <p class="golos-m" @click="forgetActive = true"> ?</p>
                            </div>
                        </div>
                        <div class="sign-form__buttons">
                            <div class="sign-form__button golos-b">
                                <a-button class="Sign-in" @click.prevent="signUser"></a-button>
                            </div>
                            <div class="sign-form__button golos-b">
                                <a-button class="Sign-up" @click="hideSign"><router-link to="/homepage/register"></router-link></a-button>
                            </div>
                        </div>
                        
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {getAuth, signInWithEmailAndPassword} from 'firebase/auth'
    export default {
        name:'a-sign',
        props:{
            show:{
                type: Boolean,
                default: false,
            }
        },
        data() {
            return {
                errMsg:'',
                eMail:'',
                password:'',
                eMailValid: false,
                passwordValid: false,
                forgetActive: false,
            }
        },  
        methods:{
            isValidEmail(){
                if (!this.eMail) {
                this.eMailValid = true;
                } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(this.eMail)) {
                this.eMailValid = true;
                }else{
                this.eMailValid = false;  
                }
            },
            isValidPassword(){
                if (!this.password) {
                this.passwordValid = true;
                } else if (this.password.length < 8) {
                this.passwordValid = true;
                }else{
                this.passwordValid = false;  
                }
            },
            validateForm(){
                this.isValidEmail(),
                this.isValidPassword()
            },
            hideSign(){
                this.$emit('update:show', false)
                this.errMsg=''
                this.eMail=''
                this.password=''
            }, 
            async signUser(){
                try{
                    const auth = getAuth();
                    await signInWithEmailAndPassword(auth, this.eMail, this.password)
                    alert('')
                }catch(error){
                    switch (error.code){
                        case 'auth/invalid-email':
                            this.errMsg=' '
                            break
                        case 'auth/user-not-found':
                            this.errMsg='   '
                            break
                        case 'auth/wrong-password':
                            this.errMsg=' '
                            break
                        default:
                            this.errMsg='   '
                            break
                    }
                }
                
            }
          

        }
    }
</script>



<style scoped>
.sign__title{
    text-align: center;
}

.sign-form__email,
.sign-form__password{
    position: relative;
}
.placeholder {
    position: absolute;
    top: 15px;
    left: 10px;
    pointer-events: none;
    transition: 0.2s ease all;
  }
  
  .input__email:focus + .placeholder,
  .input__email:not(:focus):valid + .placeholder ,
  .input__password:focus + .placeholder,
  .input__password:not(:focus):valid + .placeholder {
    top: 0;
    font-size: 12px;
    color: #888;
}

.invalid {
    display: flex;
    flex-direction: column;
    
}
.invalid small{
    line-height: 10px;
    color: red;
    font-size: 1em;
    text-indent: 1.25em;
}




.sign{
    position: fixed;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    overflow: auto;
    height: 100%;
    z-index: 1500;
}

.sign__container{
    display: flex;
    justify-content: end;

   
}

.sign__inner{
    width: 100%;
    max-width: 560px;
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    row-gap: 60px;
    min-height: 100vh;
    background: white;
    padding: 40px;

}

.sign__close {
    display: flex;
    justify-content: end;
}
.sign__close div{
    cursor: pointer;
    position: relative;
    background: #d6d6d6;
    border-radius: 50%;
    width: 30px;
    height: 30px;
}

.sign__close div::before,
.sign__close div::after{
    content: '';
    position: absolute;
    top: 14px;
    left: 5px;
    width: 70%;
    height: 3px;
    background-color: #A0A3BC;
    transform-origin: center ;
    transform: rotate(45deg);
}

.sign__close div::after{
    transform: rotate(135deg);
}

.sign__title{
    
    color: #4E4B66;
}

.sign-form__inputs{
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;
 

}
.sign-form__inputs input{
    width: 100%;
}


.sign-form__content{
    margin-bottom: 48px;
}

.sign-form__email,
.sign-form__password{
    border: 1px solid #A0A3BC;
    border-radius: 12px;
    font-weight: 500;
    padding: 16px 12px;
    display: flex;
}

.sign-form__email{
    margin-bottom: 40px;
}

.validAndForgot{
    display: flex;
    justify-content: space-between;
}


.forgotPassword{
    display: flex;
    justify-content: end;
}
.forgotPassword p:hover{
    cursor: pointer;
    color: #F56E1E;
}

.sign-form__buttons {

    display: flex;
    flex-direction: column;
    gap: 40px;
    max-width: 260px;
    margin: 0 auto;

 
}

.sign-form__button button{
    transition: all 0.5s ease ;
   
}


.Sign-in{
    background: #F56E1E;
    color: white;
    width: 100%;
    padding: 8px 15px;
    border-radius: 12px;

}

.Sign-in:hover{
    background: #e4e4e4;
    color:#F56E1E ;
}

.Sign-up{
    color: #6E7191;
    width: 100%;
    padding: 8px 15px;
    border-radius: 12px;
    
}
.Sign-up:hover{
    background: #e4e4e4;
}

.error{
    border-color: red;
    background: rgba(255, 0, 0, 0.363);
}


@media (max-width:768px) {
    .sign__inner{
        row-gap: 30px;
    }

    .sign-form__content{
        margin-bottom: 24px;
    }
    .sign-form__buttons{
        row-gap: 20px;
    }
}



</style>