<template>
    <div class="autoelektrika">
        <a-container>
            <ul class="breadcrumb golos-r">
                <li>
                    <router-link to="/homepage"></router-link>
                </li>
                <span>/</span>
                <li>
                    <router-link :to="getRouteName.path">{{getRouteName.title}}</router-link>
                </li>
            </ul>
            <h2 class="autoelektrika__title golos-sb">{{getRouteName.title}}</h2>
            <div class="autoelektrika__inner">
                <div class="autoelektrika__row">
                    <div class="autoelektrika__aside" v-if="device === 'desktop'">
                        <ul class="aside">
                            <div class="price-input-range">
                                <h3 class="golos-b">  </h3>
                                <div class="price-range">
                                    <div ref="ranges" class="price-range__container">
                                        <input type="range" min="0" max="9999" name="min" id="min" v-model.number="minValue" @input="updateRange">
                                        <input type="range" min="0" max="9999" name="max" id="max" v-model.number="maxValue" @input="updateRange">
                                    </div>
                                </div>
                                <div class="price-range-values golos-b">
                                    <p class="minRange"> {{minValue}}</p>
                                    <p  class='maxRange'> {{maxValue}}</p>
                                </div>
                            </div>
                         
                           
                        </ul>
                    </div>
                    <div class="autoelektrika__content">
                        <a-banner></a-banner>
                        <div class="autoelektrika__content_top" v-if="allCatalog">
                            <ul class="autoelektrika__subcatalogs">
                                <li class="autoelektrika__subcatalog" v-for="catalog, i of subCatalogs" :key="i">
                                    <div class="subcatalog-inner">
                                        <div class="subcatalog__img">
                                            <img :src="require('@/assets/catalogIcons/AutoElektrikaSub/' + catalog.img)" :alt="catalog.name">
                                        </div>
                                        <div class="subcatalog__text golos-b">
                                            {{ catalog.name }}
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                        <div class="autoelektrika__aside_m" v-if="device === 'mobile' || device === 'tablet' ">
                            <ul class="aside">
                                <div class="price-input-range">
                                    <h3 class="golos-b">  </h3>
                                    <div class="price-range">
                                        <div ref="ranges" class="price-range__container">
                                            <input type="range" min="0" max="9999" name="min" id="min" v-model.number="minValue" @input="updateRange">
                                            <input type="range" min="0" max="9999" name="max" id="max" v-model.number="maxValue" @input="updateRange">
                                        </div>
                                    </div>
                                    <div class="price-range-values golos-b">
                                        <p class="minRange"> {{minValue}}</p>
                                        <p  class='maxRange'> {{maxValue}}</p>
                                    </div>
                                </div>   
                            </ul>
                        </div>
                        <div class="autoelektrika__content_bottom">
                            <ul class="autoelektrika__products" v-if="allCatalog">
                                <div v-if="enablePageProducts" class="pagination__products">
                                    <a-sell-item
                                    v-for="item in pageProducts "
                                    :key="item.id"
                                    :sellItem="item"
                                    :showFav="true"

                                >
                                </a-sell-item>
                                </div>
                        
                                <div v-else class="tape__products">
                                    <a-sell-item
                                    v-for="item in products "
                                    :key="item.id"
                                    :sellItem="item"
                                    :showFav="true"

                                >
                                </a-sell-item>
                                </div>
                            </ul>
                            <div class="autoelektrika__show-more" v-if="products.length < $store.getters.calculatePriceRange.length && !enablePageProducts" @click="showMore">
                                <button class="golos-b"> </button>
                            </div>
                            <div class="autoelektrika__pagination" v-if="enablePageProducts">
                                <a-pagination
                                    ref="pagination"
                                    @check-num="getNum"
                                    :allItems="$store.getters.calculatePriceRange"
                                    :showItems="products"
                                ></a-pagination>
                            </div>

                        </div>
                    </div>
                </div>
            </div>
            <div class="toggle-show-type">
                <button class="golos-b" @click="toggleShowType">
                    {{ enablePageProducts ? '  ' : '    ()'}}
                </button>
            </div>
        </a-container>
        <a-mailing></a-mailing>

    </div>
</template>

<script>
    export default {
        data(){
            return{
                enablePageProducts:false,
                allCatalog:true,
                minValue:0,
                maxValue:9999,
            }
        },
        name:'a-autoelektrika',
        computed:{
            subCatalogs(){
                return this.$store.state.AutoElektrikaCatalogs[0].subCatalogs
            },
            products(){
                return this.$store.getters.catalogLimitProducts
            },
            page(){
                return this.$store.state.page
            },
            pageProducts(){
                return this.$store.getters.pageProducts
            },
            getRouteName(){
                const currentPath = this.$route.path
                
                return this.$router.options.routes.find(route => route.path === currentPath)
            }
        
           
            
        },
        methods:{
            showMore(){
                this.$store.state.page++;
                this.$store.state.limitInCatalog = this.page*8
                
            },
            getNum(data){   
                this.$store.state.page = Number(data) - 1;
            },
            toggleShowType(){
                this.$store.state.page = 1
                this.enablePageProducts = !this.enablePageProducts
                this.$store.state.limitInCatalog = 8;
              
                
            },
            updateRange(){
              
                 this.$store.state.minRange = this.minValue;
                 this.$store.state.maxRange = this.maxValue;
                const percent1 = (this.minValue / 9999) * 100;
                const percent2 = (this.maxValue / 9999) * 100;
                this.$refs.ranges.style.background = `linear-gradient(to right, #F1F1F1 ${percent1}% , #F56E1E ${percent1}% , #F56E1E ${percent2}%, #F1F1F1 ${percent2}%)`
                if(this.minValue > this.maxValue){
                    this.minValue = this.maxValue
                    
                }

                this.$store.state.page = 1
                if(this.enablePageProducts){
                    console.log(this.$refs.pagination.$refs.dot[0].classList.add('active'))
                }
          
            }
        },
        mounted(){
            this.updateRange()
            this.getRouteName
        }
       
    }
</script>

<style scoped>
.autoelektrika__aside{
 
    flex-basis: 20%;
}

.autoelektrika__aside_m{
    margin: 20px 0;
    padding: 15px 0;
    border-top:3px solid #F1F1F1;
    border-bottom:3px solid #F1F1F1;
}

.aside{
    display: flex;
    flex-direction: column;
}
.price-range{
   position: relative;
   height: 30px;
   

}
.price-range__container{
    width: 100%;
    height: 3px;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    background: #F56E1E;
    border-radius: 5px;
}


.price-range-values{
    display: flex;
    justify-content: space-between;
}

input[type='range']{
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    width: 100%;
    outline: none;
    position: absolute;
    margin: auto;
    top: 0;
    bottom: 0;
    pointer-events: none;
    accent-color: #F56E1E;
}

input[type="range"]::-webkit-slider-thumb {
    pointer-events: all;
    position: relative;
    z-index: 1;
    outline: 0; 
   cursor: pointer;
}

















.tape__products,
.pagination__products{
    display: grid;
    grid-template:auto /  repeat(4, 1fr);
    gap: 30px;
}
.toggle-show-type{
    margin-top: 30px;
    display: flex;
    justify-content: center;
}
.toggle-show-type button{
    color: red;
    margin: 0 auto;
}
.autoelektrika__title{
    margin-bottom: 40px;
}
.autoelektrika__row{
    display: flex;
    justify-content: space-between;
    gap: 30px;
}

.autoelektrika__content{
    flex: 0 1 80%;
}
   
.autoelektrika__subcatalogs{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(165px, 240px));
    gap: 30px;
    justify-content: center;
   
}
.subcatalog-inner{
    background-color: #FFF;
    border-radius: 12px;
    border:1px solid #fff;
    cursor: pointer;
    padding: 12px 14px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    text-align: center;
    
    
}

.subcatalog-inner:hover{
    border:1px solid #F56E1E;
}

.autoelektrika__content{
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/*.autoelektrika__products{
    display: grid;
    grid-template:auto /  repeat(4, 1fr);
    gap: 30px;
}*/


.autoelektrika__content_bottom{
    display: flex;
    flex-direction: column;
    gap: 20px;
}
.autoelektrika__show-more{
    border-radius: 12px;
    background: var(--Gray-05, #F1F1F1);
    padding: 8px 16px;
    text-align: center;
}

.autoelektrika__show-more:hover {
    outline: 1px solid #F56E1E;
}

@media (max-width:1024px){
    .autoelektrika__content{
        flex: 1 1 100%;
    }
}

@media (max-width:768px) {
   
    .tape__products,
    .pagination__products{
       
        grid-template:auto /  repeat(2, 1fr);
    }
}


</style>