<template>
    <section class="register">
        <a-container>
            <ul class="breadcrumb golos-r">
                <li>
                    <router-link to="/homepage"></router-link>
                </li>
                <span>/</span>
                <li>
                    <router-link to="/register"> </router-link>
                </li>
            </ul>
            <h1 class="register__title golos-sb"> </h1>
            <form action="#" class="register__form" @submit.prevent="submitForm">
                <fieldset class="personal">
                    <legend class="personal__title golos-b"> </legend>
                    <div class="personal-form__wrapper inputs-wrapper">
                        <a-input 
                            :className="'fisrtname'" 
                            :lengthMin="2" 
                            :req="required"
                            :label_text="''"></a-input>
                        <a-input 
                            :className="'name'" 
                            :lengthMin="3" 
                            :req="required"
                            :label_text="''"></a-input>
                        <a-input 
                            :className="'lastname'"
                            :label_text="''"
                            :lengthMin="2"

                            ></a-input>
                    </div>
                </fieldset>
                <hr>
                <fieldset class="contact">
                    <legend class="contact__title golos-b"> </legend>
                    <div class="contact-form__wrapper inputs-wrapper">
                        <a-input
                            :className="'email'"
                            :valid="inputValid.email"
                            @input="(event) => handleInput(event, 'email')"
                            :type="'email'"
                            :label_text="' .'"
                            ref="email"
                        >
                        </a-input>
                        <a-input
                            :className="'phone-contact'"
                            :valid="inputValid.phone_contact"
                            @input="(event) => handleInput(event, 'tel', 'phone-contact__input')"
                            :label_text="''"
                            :req="required"
                        >
                        </a-input>
                    </div>
                </fieldset>
                <hr>
                <fieldset class="company">
                    <legend class="company__title golos-b">  </legend>
                    <div class="company-form__wrapper inputs-wrapper">
                        <div class="company-tabs golos-m" >
                            <div class="company-tab__ip" :class="{active: activeButton==='ip'}" @click="toggleTabs('ip')">
                                <p></p>
                            </div>
                            <div class="company-tab__legal-person" :class="{active: activeButton==='legal'}" @click="toggleTabs('legal')">
                               <p> </p>
                            </div>
                        </div> 
                        <a-input
                            :className="'descValue'"
                            :lengthMin="5"
                            :req="required"
                            :label_text="' '"
                        ></a-input>  
                        <a-input
                            :className="'INN'"
                            :req="required"
                            :label_text="''"
                            @input="handleInput"
                            :valid="inputValid.numINN"
                        ></a-input>  
                        <a-input 
                            v-if="activeButton==='legal'"
                            :className="'KPP'"
                            :req="required"
                            :label_text="''"
                            @input="handleInput"
                            :valid="inputValid.numKPP"
                        ></a-input>  
                    </div>
                </fieldset>
                <hr>
                <fieldset class="legal-adress">
                    <legend class="legal-adress__title golos-b"> :</legend>
                    <div class="legal-adress-form__wrapper inputs-wrapper"  >
                        <a-select
                        :items="ountries"
                        :placeholder="''"
                        :className="'legal-country'"
                        @selectCountry="updateRegions"
                        ></a-select>
                        <a-combobox
                            :classname="'legal-region'"
                            :options="countryRegions"
                        ></a-combobox>
                        <a-input
                            :classname="'adress'"
                            :type="'text'"
                            :lengthMin="10"
                            :label_text="''"
                            :req="required"
                            :small_text="', , , '"
                        ></a-input>
                        <a-input
                            :classname="'zip'"
                        ></a-input>
                        <a-input
                            :className="'phone-legal'"
                            :type="'tel'"
                            :valid="inputValid.phone_legal"
                            @input="(event) => handleInput(event, 'tel','phone-legal__input')"
                            :label_text="''"
                            :req="required"
                        >
                        </a-input>
                    </div>
                </fieldset>
                <hr>
                <fieldset class="delivery">
                    <legend class="delivery__title golos-b"> :</legend>
                    <div class="delivery-form__wrapper inputs-wrapper">
                        <a-select
                        :items="ountries"
                        :placeholder="''"
                        :className="'delivery-country'"
                        @selectCountry="updateRegions"
                        ></a-select>
                        <a-combobox
                            :classname="'delivery-region'"
                            :options="countryRegions"
                        ></a-combobox>
                        <a-input
                            :classname="'adress'"
                            :type="'text'"
                            :lengthMin="10"
                            :label_text="''"
                            :small_text="', , , '"
                        ></a-input>
                        <a-input
                            :className="'phone-delivery'"
                            :type="'tel'"
                            :valid="inputValid.phone_delivery"
                            @input="(event) => handleInput(event, 'tel','phone-delivery__input')"
                            :label_text="''"
                        >
                        </a-input>
                    </div>
                </fieldset>
                <hr>
                <fieldset class="pass">
                    <legend class="pass__title golos-b">:</legend>
                    <div class="pass-form__wrapper inputs-wrapper">
                        <a-input
                            :className="'pass-enter'"
                            :lengthMin="9"
                            :req="required"
                            :label_text="' '"
                             ref="passEnter"
                        ></a-input> 
                         <a-input
                            :className="'pass-repeat'"
                            :lengthMin="9"
                            :req="required"
                            :label_text="' '"
                            ref="passRepeat"
                        ></a-input>   
                    </div>
                </fieldset>
                <hr>
                <fieldset class="subscribes">
                    <legend class="subscribes__title golos-b"></legend>
                    <div class="subscribes__wrapper">
                        <div class="subscribe-wrapper" v-for="subscribe in subscribes" :key="subscribe.id">
                            <a-checkbox :id="subscribe.id" :active="subscribe.active" :label_text="subscribe.label" :value="subscribe.value" v-model:isCheck="subscribe.active" @update:isCheck="getCheck(subscribe.id)"></a-checkbox>
                            <div class="subscribe-btn-wrapper">
                                <button type="button" class="subscribe-btn golos-m" :class="{active: subscribe.active}" @click="toggleSub(subscribe)" v-text="subscribe.active ? '':''"></button>
                            </div>
                        </div>
                    </div>
                    <div class="form-consent">
                        <div class="form-consent__content golos-r">
                            <input type="checkbox" name="consent" id="consent" :required="required">
                            <label for="consent">      
                                  c <a class="mark" href="#">   </a></label>
                        </div>
                    </div>
                </fieldset>
                <div class="sign">
                    <button class="submit golos-b" type="submit" ></button>
                </div>
                <button class="test golos-b" @click.prevent="resetReq"> required</button>
            </form>
        </a-container>
    </section>
</template>

<script>
    import {getAuth, createUserWithEmailAndPassword} from "firebase/auth"
    export default {
        name:'a-register-page',
        data(){
            return{
                required:true,
                subscribes:[
                    {id:0, label:'', value:'mailing',  active:false},
                    {id:1, label:'-', value:'price',  active:false},
                    {id:2, label:'', value:'catalog',  active:false},
                    
                ],
                selectedSubscribes:[],
                selectRegion:'',
                inputValid:{
                    email: false,
                    phone_contact: false,
                    phone_legal: false,
                    phone_delivery: false,
                    text: false,
                    numINN:false,
                    numKPP:false,
                },
                showDrop:'',
                activeButton:'ip',
                defaultCountry:{},
                ountries:[
                    {id:1, name:'', key:'ru'},
                    {id:2, name:'', key:'cn'},
                    {id:3, name:'', key:'us'},
                    {id:4, name:'', key:'ge'},
                ],
                countryRegions:[],
                regions:[
                   {country:'', 
                   region:[
                            {
                                id: 80,
                                name: "   ",
                                district_id: 1
                            },
                            {
                                id: 81,
                                name: "-  ",
                                district_id: 3
                            },
                            {
                                id: 2,
                                name: " ",
                                district_id: 2
                            },
                            {
                                id: 3,
                                name: " ",
                                district_id: 5
                            },
                            {
                                id: 4,
                                name: " ",
                                district_id: 4
                            },
                            {
                                id: 5,
                                name: " ",
                                district_id: 3
                            },
                   ]},
                   {
                    country:'',
                    region:[
                            {
                                id: 82,
                                name: "Baden-Wrttemberg",
                                district_id: 1
                            },
                            {
                                id: 83,
                                name: "Berlin",
                                district_id: 3
                            },
                            {
                                id: 6,
                                name: "Bayern",
                                district_id: 2
                            },
                            {
                                id: 7,
                                name: "Mecklenburg-Vorpommern",
                                district_id: 5
                            },
                            {
                                id: 8,
                                name: "Hamburg",
                                district_id: 4
                            },
                            {
                                id: 9,
                                name: "Thringen",
                                district_id: 3
                            }, 
                    ]
                   },
                   {
                    country:'',
                    region:[
                            {
                                id: 82,
                                name: "Michigan",
                                district_id: 1
                            },
                            {
                                id: 83,
                                name: "Rhode Island",
                                district_id: 3
                            },
                            {
                                id: 6,
                                name: "Colorado",
                                district_id: 2
                            },
                            {
                                id: 7,
                                name: "Arizona",
                                district_id: 5
                            },
                            {
                                id: 8,
                                name: "Nevada",
                                district_id: 4
                            },
                            {
                                id: 9,
                                name: "Texas",
                                district_id: 3
                            }, 
                    ]
                   },
                   {
                    country:'',
                    region:[
                            {
                                id: 82,
                                name: "Beijing",
                                district_id: 1
                            },
                            {
                                id: 83,
                                name: "Fujian",
                                district_id: 3
                            },
                            {
                                id: 6,
                                name: "Shaanxi",
                                district_id: 2
                            },
                            {
                                id: 7,
                                name: "Shanghai",
                                district_id: 5
                            },
                            {
                                id: 8,
                                name: "Tibet",
                                district_id: 4
                            },
                            {
                                id: 9,
                                name: "	Liaoning",
                                district_id: 3
                            }, 
                    ]
                   }
                    
                ]
            }
        },
        computed:{
            currentPath(){
                return this.$route.path
            },
       
          
        },
     
        methods:{
            handleInput(event, type, className) {
                
                const inputValue = event.target.value;
                const regexpMail = /^[\w-]+(\.[\w-]+)*@([\w-]+\.)+[a-zA-Z]{2,7}$/;
                const regexpTel = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/
                

                if (type==='email' && regexpMail.test(inputValue)) {
                    this.inputValid.email = true
                }else{
                    this.inputValid.email = false
                }


                if(type==='tel') {
                  event.target.value = event.target.value.replace(/[^+\d.]/g, '')
               
                  if(regexpTel.test(inputValue)) {
                        switch (className) {
                            case 'phone-legal__input':
                                this.inputValid.phone_legal = true
                                console.log('ok')
                                break;
                            case 'phone-delivery__input':
                                this.inputValid.phone_delivery = true
                                break;
                             case 'phone-contact__input':
                                this.inputValid.phone_contact = true
                                break;   
                            default:
                                break;
                        }
                  }else{
                        this.inputValid.phone_legal = false
                        this.inputValid.phone_contact = false
                        this.inputValid.phone_delivery = false
                  }
                }

                if(event.target.classList.contains('INN__input')){
                    event.target.value = event.target.value.replace(/[^\d.]/g, '')
                    if(event.target.value.length === 10){
                        this.inputValid.numINN = true
                    }else{
                        this.inputValid.numINN = false
                    }
                }

                if(event.target.classList.contains('KPP__input')){
                    event.target.value = event.target.value.replace(/[^\d.]/g, '')
                    if(event.target.value.length === 9){
                        this.inputValid.numKPP = true
                    }else{
                        this.inputValid.numKPP = false
                    }
                }



                

                
            },
            toggleTabs(button){
               this.activeButton = (this.activeButton === button) ? 'ip' : button;

            },
            updateRegions(selectedCountry){
                this.countryRegions = []
                this.countryRegions = this.regions.filter(region => region.country === selectedCountry).find(region => region.region)
                this.countryRegions = this.countryRegions.region



                // if(selectedCountry === ''){
                //     this.countryRegions.push(this.regions.find(region => region.ru).ru)
                // }
                // // }else if(selectedCountry === ''){
                // //     this.countryRegions.push(this.regions.find(region => region.ge).ge)
                // // }else if(selectedCountry === ''){
                // //     this.countryRegions.push(this.regions.find(region => region.us).us)
                // // }else if(selectedCountry === ''){
                // //     this.countryRegions.push(this.regions.find(region => region.cn).cn)
                // // }
                // this.countryRegions = this.countryRegions.filter(e => e)
                // console.log(this.countryRegions)
            },
         
            resetReq(el){
                this.required = !this.required
                el.target.innerText = (el.target.innerText === ' required') ? ' required' : ' required';
               
            },
            submitForm(){
            
                console.log(this.selectedSubscribes);

                for(const subscribe of this.subscribes){
                    subscribe.active = false
                }
                this.getCheck()
                this.selectedSubscribes=[]
                this.registerUser()
            
            },  
            async registerUser(){
                if (this.$refs.passEnter.inputValue === this.$refs.passRepeat.inputValue ){
                    if(this.$refs.passEnter.inputValue.length < 8){
                        alert('    8- !')
                    }else{
                        try{
                            const auth = getAuth()
                            await createUserWithEmailAndPassword(auth, this.$refs.email.inputValue, this.$refs.passEnter.inputValue  )
                            alert('  !')
                            // await fetch('https://autoel-default-rtdb.firebaseio.com/Users', {
                            //     method:'POST',
                            //     headers:{'Content-Type': 'application/json'},
                            //     body:JSON.stringify(this.$refs.email.inputValue, this.$refs.passEnter.inputValue)
                            // })
                            
                          
                        }catch(error){
                            console.log(error)
                            alert(error.message)
                        }
                    } 
                }
               else{
                    alert('  !')
               }
            },
            toggleSub(sub){
                sub.active=!sub.active
          
                this.getCheck()
            },
            getCheck(){
                this.selectedSubscribes = this.subscribes
                                                        .filter(sub=> sub.active)
                                                        .map(sub=> sub.value)
                
                const cards = document.querySelectorAll('.subscribe-wrapper')
                this.subscribes.forEach(sub => {
                    sub.active ? cards[sub.id].classList.add('active') : cards[sub.id].classList.remove('active')
                })

              
              
               
            }
          
       
        },
      

        
    }
</script>

<style scoped>
.test{
    color: red;
}
.register__form{
    width: 100%;
    max-width: 1170px;
    display: flex;
    flex-direction: column;
    row-gap: 20px;
}

fieldset{
    border: none;
    
}
legend{
    margin-bottom: 24px;
}

.inputs-wrapper{
    display: flex;
    gap:16px;
    flex-wrap: wrap;
    justify-content: space-between;
}



.input-wrapper{
    flex: 0 1 47%;
}



.company-tabs{
    flex: 0 1 47%;
    display: grid;
    grid-template: 1fr / 1fr 1fr;
    background:#F1F1F1;
    padding: 8px 6px;
    border-radius: 12px;
    align-items: center;
}

.company-tabs div{
    text-align: center;
    flex: 0 1 50%;
    padding:8px 12px;
    position: relative;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
 
}


.company-tabs div.active{
    border-radius: 8px;
    background:  #F56E1E;
    color: #F1F1F1;
}


.legal-adress-form__wrapper .input-wrapper:nth-child(3),
.delivery-form__wrapper .input-wrapper:nth-child(3) {
    flex: 1 1 100%;
}

.sign{
    display: flex;
    justify-content: center;
}
.submit{
    border-radius: 12px;
    background:  #F56E1E;
    color: #F1F1F1;
    padding: 8px 12px;
    display: flex;
    flex: 0 1 30%;
    justify-content: center;
    transition: all 0.3s ease;
  
}

.submit:hover{
    background-color: #e7e6e6;
    color: #F56E1E;
}





.subscribes__wrapper{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 30px;
    margin-bottom: 60px;


}



.subscribe-btn-wrapper{
    margin-top: 24px;
}
.subscribe-btn{
    border-radius: 8px;
    border: 1px solid var(--gray-04, #6E7191);
    padding: 5px 12px;
    position: relative;
}

.subscribe-btn:hover{
    border: 1px solid #F56E1E;
    color: #F56E1E;
}


.subscribe-wrapper{
    border-radius: 12px;
    background:#F8F8F8;
    gap:22px;
    padding: 12px;
    display: flex;
    justify-content: space-between;
    width: 330px;

}

.subscribe-wrapper:hover{
    background: #F1F1F1;
    outline: 1px solid  #DE5703;
}

.subscribe-wrapper.active{
    background-color: #FCE1D2;
}


.subscribe-btn.active{
    border: 1px solid var(--orange, #F56E1E);
    color: #F56E1E;
    padding-right: 24px;
}

.subscribe-btn.active::after{
    content:'';
    position: absolute;
    width: 16px;
    height: 16px;
    background: url('../assets/active-subscribe.svg');
    right: 4px;
    
}

.form-consent{
    border-radius: 12px;
    background:  #F1F1F1;
    padding: 12px 18px;
}

.form-consent__content{
    font-size: 12px;
    display: flex;
    gap: 8px;
    position: relative;
    padding-left: 20px;
    max-width: 430px;
    margin: 0 auto;
}

.form-consent__content input{
    position: absolute;
    width: 100%;
    height: 100%;
   
}

.form-consent__content label {
    padding-left: 24px;
}


.form-consent__content input::before{
    content: '';
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    width:16px;
    height: 16px;
    border: 1px solid #F56E1E;
   

}

.form-consent__content input:checked::before{
    
    background: url('../assets/quadrChecked.png') no-repeat;
    background-size: contain;
  

}







@media (max-width:768px) {
    .inputs-wrapper{
        flex-direction: column;

    }

    .legal-adress .inputs-wrapper{
        display: grid;
        grid-template: 1fr 1fr/ 1fr;
    }
    .input-wrapper{
        flex: 0 1 100%;
    }
    .subscribes__wrapper{
        flex-direction: column;
        max-width: 330px;
        margin: 0 auto;
    }
    .subscribe-wrapper{
        max-width: 330px;
        width: auto;
    }
}


@media (max-width:399px) {
    .subscribes__wrapper{
        justify-content: start;
    }
    .subscribe-wrapper{
        flex-direction: column;
        align-items: center;
    
    }
    .subscribe-btn-wrapper{
        margin-top: 0;
    }

    .form-consent__content{
        font-size: 10px;
    }
}

</style>