<template>
    <div :class="className" class="checkbox-wrapper">
        <div :class="className" class="checkbox-inner">
            <input type="checkbox" 
                :class="className"
                :name="name" 
                :id="id"
                :value="value"
                v-model="model"
                >
            <div class="checkbox-button" :class="{checked:active}"></div>
            <label :for="getRandom" class="golos-m" :class="{checked:active}" >{{label_text}}</label>
        </div>
       
    </div>
</template>

<script>
    export default {
        name:'a-checkbox',
        props:{
            className:String,
            name:String,
            value:[String, Boolean, Object],
            label_text:[String, Number],
            active:Boolean,
            isCheck: Boolean,
            id:Number,
        },
        data() {
            return {
                
                
            }
        },
        computed:{
            getRandom(){
                return Math.random()
            },
            model:{
                get() {
                    return this.isCheck ;
                  
                
                    
                },
                set(val) {
                    this.$emit("update:isCheck", val);
                    
                    
                  
                }
            }
           
        },
        methods:{
          click(){
            console.log(this.model)
          }
        },
        mounted(){
           
        }
    }
</script>

<style scoped>
      
    .checkbox-inner{
        position: relative;
        display: flex;
        gap: 10px;
        align-items: center;
    }
    .checkbox-inner input{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 5;
    }

    .checkbox-button{
        position: relative;
        min-width: 24px;
        min-height: 24px;
        border-radius: 50%;
        border: 2px solid #6E7191;
      
    }
    .checkbox-button.checked{
       background-color: #DE5703;
       border: 2px solid #DE5703;
    }

    .checkbox-button.checked::before,
    .checkbox-button.checked::after{
        content: '';
    }

    .checkbox-button::before,
    .checkbox-button::after{
        content: none;
        position: absolute;
        background-color: white;
        width: 60%;
        height: 3px;
        border-radius: 20%;
        left: 30%;
        top: 40%;
        
    }

    .checkbox-button::before{
        transform: rotate(-50deg);
    }

    .checkbox-button::after{
        transform: rotate(40deg);
        width: 30%;
        left: 20%;
        top: 50%;
    }
    label.checked{
        color: #DE5703;  
    }

   

</style>