<script>
    export default {
        name:'a-phone',
        data() {
            return {
                defaultPhone:{id:1, phone:'8 (800) 455-45-11'},
                phones:[
                    {id:1, phone:'8 (800) 455-45-11'},
                    {id:2, phone:'+7 (495) 108-54-64'}
                ]
            }
