<template>
    <div class="combobox-wrapper" :class="[className, {opened:dropdownOpen}]">
        <div class="combobox-content">
            <input class="combobox-input golos-m" placeholder="" type="text" 
            v-model="searchText" 
            @input="handleInput" 
            @keydown.down="highlightNext" 
            @keydown.up="highlightPrev"
            @focus="dropdownOpen=true"
            @blur="dropdownOpen=false"
            >

            <div class="combobox-dropdown-wrapper" v-if="dropdownOpen">
                <ul class="combobox-dropdown-list golos-m">
                    <li class="combobox-dropdown-options" v-if="filteredOptions.length === 0"  >
                        <p v-text="options.length === 0 ? ' ' : ' '"></p>
                    </li>
                    <li v-else
                        v-for="(option, index) in filteredOptions" 
                        class="combobox-dropdown-options"
                        :key="index" 
                        :class="{ active: index === activeIndex }" 
                        @mousedown="selectOption(option.name)"
                    ><p class="combobox-dropdown-option">{{option.name }}</p>
                
                </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'a-combobox',
        props:{
            options: Array,
            placeholder: String,
            className: String
        },
        data() {
            return {
                searchText: '',
                dropdownOpen: false,
                activeIndex: -1
            };
        },
        computed: {
            filteredOptions() {
                return this.options.filter(option => option.name.toLowerCase().includes(this.searchText.toLowerCase()));
              
            
            }
        },
        methods: {
            handleInput() {
                this.dropdownOpen = true;
                this.activeIndex = -1;
            },
            selectOption(option) {
                this.searchText = option;
                this.dropdownOpen = false;
            },
            highlightNext() {
                if (this.activeIndex < this.filteredOptions.length - 1) {
                    this.activeIndex++;
                }   
            },
            highlightPrev() {
                if (this.activeIndex > 0) {
                    this.activeIndex--;
                }
            }
        },
        mounted(){
            
        }
       
    }
</script>

<style scoped>



.combobox-wrapper{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 12px;
    border-radius: 12px;
    border: 1px solid #A0A3BC;
    background: #FFF;
    min-width: 305px;
    cursor: pointer;
    flex: 0 1 47%;
}
.opened{
    border-color:#F56E1E;
}
.combobox-content{
    width: 90%;
}

.combobox-input{
    width: 100%;
    font-size: 16px;
}

.combobox-input::placeholder{
    font-weight: 400;
    color: #A0A3BC;
    font-size: 16px;
}

.combobox-dropdown-wrapper{
    position: absolute;
    width: 100%;
    min-height: 100%;
    top: 100%;
    left: 0;
    padding-top: 5px;
    z-index: 999;
}

.combobox-dropdown-list{
    display: flex;
    flex-direction: column;
    gap: 5px;
    padding: 12px;
    border-radius: 12px;
    background: #FFF;
    box-shadow: 0 0 2px 1px #A0A3BC;
}



@media (max-width:399px){
    .combobox-wrapper{
        min-width: 180px;
    }
}



</style>