<template>
    <nav class="submenu-nav" >
        <ul class="submenu-list" v-for="item, i in subMenuList" :key="item.id" >
            <li class="submenu-list__li golos-m" :class="{open : item.open}"   @click="showDropdown(i)">
                <div class="submenu-list__row">
                    <p>{{item.name}}</p>
                    <div class="submenu-list__arrow subheader__arrow" v-if="item.dropdownList">
                        <svg width="16" height="16" viewBox="0 0 16 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M3 6L8 11L13 6" stroke="#A0A3BC" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                        </svg>  
                    </div>
                </div>
                <div class="dropdown-list-wrapper" v-if="item.dropdownList && (device ==='desktop' || device === 'tablet')">
                    <ul class="dropdown-list" >
                        <li class="dropdown-list__li"  v-for="subItem in item.dropdownList" :key="subItem.id">
                            <a href="#">{{subItem.name}}</a>
                        </li>
                    </ul>
                </div>      
            </li>
            <div class="dropdown-list__mobile golos-r" v-if="item.dropdownList && device === 'mobile'" :class="{_show: item.open}">
                <ul class="dropdown-list">
                    <li class="dropdown-list__li"  v-for="subItem in item.dropdownList" :key="subItem.id">
                        <a href="#">{{subItem.name}}</a>
                    </li>
                </ul>
            </div>
        </ul>
    </nav>
</template>

<script>
    export default {
        name:'a-submenulist',
        data() {
            return {
                subMenuList:[
                    {id:1, name:' ', open:false, 
                    dropdownList:[
                        {id:1, name:'  '},
                        {id:2, name:''},
                        {id:3, name:''},
                        {id:4, name:''},
                        {id:5, name:''}
                    ]
                
                    },
                    {id:2, name:'', open:false,     
                    dropdownList:[
                        {id:1, name:''},
                        {id:2, name:' '},
                        {id:3, name:'  '},
                        {id:4, name:' '},
                        {id:5, name:' '},
                        {id:6, name:'  '},
                        {id:7, name:'  '}
                    ]
                    } ,
                    {id:3, name:''}
                    
                    
                ],
            }
        },
        methods: {
            showDropdown(i){
                this.subMenuList.forEach((item, index) => {
                    if (index !== i) {
                        item.open = false; //    
                    }
                });
                this.subMenuList[i].open  = !this.subMenuList[i].open  
                 

            },
           
        },
    }
</script>

<style scoped>


.submenu-nav{
    display: flex;
   
    gap: 18px;
}


.submenu-list li,.submenu-list__row{
    cursor: pointer;
   
  
}
.submenu-list li,.submenu-list__row{
    transition: all 0.2s ease;
}

.submenu-list__li{
    position: relative;
    font-weight: 700;
}

.submenu-list__row{
    display: flex;
    gap: 4px;
    border-radius: 8px;
    padding:4px 8px;
}

.submenu-list__arrow svg path{
    transition: all 0.4s ease;
 
}

.footer-nav{
    gap: 5px;
}

.footer-nav .submenu-list{
    flex: 0 1 33.3333%;
}

.footer-nav .submenu-list svg{
    display: none;
}

.footer-nav .dropdown-list-wrapper{
    visibility: visible;
    opacity: 1;
    position: relative;
    display: flex;
   

}
.footer-nav .dropdown-list{
    max-height: max-content;
    background: none;
    box-shadow: none;
}


.footer-nav .dropdown-list-wrapper{
    width: auto;
    
}

.footer-nav .dropdown-list__li, .footer-nav .dropdown-list-wrapper{
    padding: 4px 0;
}

.footer-nav .dropdown-list__li:hover{
    color: #A0A3BC;
    background-color: inherit;
}
.footer-nav .submenu-list__li:hover .submenu-list__row{
    background: inherit;
    color: inherit;
}
.footer-nav .submenu-list__row{
    padding: 0;
}
.footer-nav .submenu-list__row:hover p{
    color: #A0A3BC;
}
@media (max-width:1024px) {
    .submenu-list{
        gap: 7px;
    }
    .submenu-list__li{
        font-size: 12px;
    }
}


@media (min-width: 768px){
    .submenu-list__li:hover .submenu-list__row{
        background:  #F1F1F1;
        color: #F56E1E;
    }
    
    .submenu-list__li:hover .submenu-list__arrow svg path{
        transform: scaleY(-1) translateY(-2px) ;
        transform-origin: center;
        stroke: #F56E1E;
    }
    
    .submenu-list__li:hover .dropdown-list-wrapper{
        visibility: visible;
        opacity: 1;
        max-height: none;
    }
    .dropdown-list-wrapper{
        position: absolute;
        transition: opacity 0.3s ease;
        visibility: hidden;
        opacity: 0;
        width: 194px;
        max-height: 0;
        z-index: 5;
        top: 100%;
        left: 0;
        padding: 5px;
    }
    .dropdown-list{
        display: inline-flex;
        flex-direction: column;
        gap: 4px;
        padding: 4px;
        border-radius: 12px;
        background: #FFF;
        /* shadow 3 */
        box-shadow: 0px 2px 16px 0px rgba(0, 0, 0, 0.20);
    
    }
    
 
    .dropdown-list__li{
        padding: 8px;
        font-weight: 400;
        border-radius: 8px;
    }
    
    .dropdown-list__li:hover{
        color: #FFF;
        background-color: #F56E1E;
    }
    
}


@media (max-width: 768px){
    .footer-nav{
        display: none;
    }
    .submenu-nav{
        flex-direction: column;
    
    }
    .submenu-list__row{
        font-size: 16px;
        padding: 6px 10px;
        background:  #F1F1F1;
        justify-content: space-between;
    }
    .dropdown-list__mobile{
        transition: all 0.3s ease;
        opacity: 0;
        visibility: hidden;
        transition: all 0.5s ease;
        max-height: 0;
        
    }

    .dropdown-list__mobile._show{
        opacity: 1;
        visibility: visible;
        max-height: 400px;
    }
    .dropdown-list{
        padding-top: 11px;
        display: flex;
        flex-direction: column;
        gap: 20px;
    }
    .dropdown-list__li{
        font-size: 14px;
    }
    .dropdown-list__li:hover{
        color: #F56E1E;
    }

    .submenu-list__li.open .submenu-list__row{
     
        color: #F56E1E;
    }
    
    .submenu-list__li.open .submenu-list__arrow svg path{
        transform: scaleY(-1) translateY(-2px) ;
        transform-origin: center;
        stroke: #F56E1E;
    }
    
  


}

</style>