<template>
    <ul class="breadcrumbs">
        <li class="breadcrumb">
            <router-link to="/"></router-link>
        </li>
        <li class="breadcrumb" v-for="link of links" :key="link.text">
            <router-link :to="link.to">{{link.text}}</router-link>
        </li>
       
       
    </ul>
</template>

<script>
    export default {
        computed:{
            links(){
               const breadcrumbs = []
               const routeHierarchy = this.$route.matched

              routeHierarchy.forEach(route =>{
                if(route.path !=='/' && route.path !=='/homepage' ){
                    breadcrumbs.push({
                        to: this.$route.path,
                        text: this.$route.name
                    })
                }
               })

               return breadcrumbs
            }
        },
        methods:{
            
        }
    }
</script>

<style  scoped>

.breadcrumbs{
    display: flex;
}

</style>