<template>
        <div :class="[className, {focused:isFocused}]" class="input-wrapper">
            <div :class="className + '__input-inner'" class="input-inner">
                <input 
                :type="type" class="golos-m" 
                :class="className + '__input'" 
                :required="req" 
                :minlength="lenghtMin" 
                :maxlength="lenghtMax" 
                :id="getRandom" 
                @focus="isFocused=true" 
                @blur="isFocused=false"
                v-model="inputValue"
                >
                <label 
                :for="getRandom" 
                :class="{on : isFocused || inputValue.length > 0}" 
                class="placeholder golos-r">
                    {{label_text}} <span v-if="req" class="red">*</span>
                </label>
            </div>
            <span :class="[className + '__valid', {valid: inputValue.length >= lengthMin || valid}]"></span>
            <small class="golos-r" v-if="small_text">{{small_text}}</small>
        </div>

</template>

<script>
    export default {
        name:'a-input',
        props:{
            className: String,
            type:String,
            req: Boolean,
            lengthMin:Number,
            lenghtMax:Number,
            label_text:String,
            valid: Boolean,
            small_text:String,
        

        },
        data(){
            return{
                inputValue:'',
                isFocused: false,
            }
        },
        computed:{
            getRandom(){
                return Math.random()
            },
        },
        methods:{
           
        
        }
    }
</script>

<style scoped>
  
    small{
        position: absolute;
        top: 100%;
        left: 10px;
    }
    .input-wrapper{
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 15px 12px;
        border-radius: 12px;
        border: 1px solid #A0A3BC;
        background: #FFF;
        min-width: 305px;
    }
    .focused{
        border-color:#F56E1E;
       }

    .input-inner{
        width: 90%;
    }
    input{
        margin-top: 5px;
        width: 100%;
      
    }
    .placeholder{
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 10px;
        pointer-events: none;
        transition: 0.2s ease all;
        font-size: 16px;
        color: #A0A3BC;
        
    }
    .valid{
        width: 20px;
        display: block;
        background: url('../../assets/valid.svg') no-repeat;
        
    }


   .placeholder.on{
    top: 6px;
    transform: translateY(0);
    font-size: 12px;
}

.red{
    color: #DE4503;
}



@media (max-width:399px) {
    .input-wrapper{
        width: 100%;
        min-width: 180px;
    }
}
</style>