<template>
    <li class="sell-item">
        <div class="sell-item__inner"  :class="{inactive: sellItem.lot === 0 }">
            <div class="sale" v-show="sellItem.sale">
                <p class="golos-m"></p>
            </div>
            <div class="fav" v-show="showFav">
                <div class="add-fav" :class="{added: sellItem.added}" @click="addToFav(sellItem.id)"></div>
            </div>
            <div class="sell-item__img">
                <img :src="require('../../assets/sellitems/' + sellItem.img)" :alt="sellItem.img">
            </div>
            <div class="sell-item__col">
                <div class="sell-item__price">
                    <span class="sell-item__price_new golos-sb">{{sellItem.priceNew}} </span>
                    <sup v-show="sellItem.sale" class="sell-item__price_old golos-b">{{sellItem.priceOld}} </sup>
                </div>
                <div class="sell-item__desc golos-m">{{sellItem.desc}}</div>
                <div class="sell-item__id">
                    <p class="golos-m">.:</p>
                    <p class="golos-sb">{{sellItem.katNum}}</p>
                </div>
            </div>

            <div class="sell-item__buys" v-if="sellItem.count > 0">
                <div class="sell-item__add-item" @click="addToCart(sellItem.id)">
                    <div class="item-add"></div>
                </div>
                <div class="sell-item__count">
                    <input class="golos-m" type="number" 
                        v-removezero
                        :value="sellItem.count" 
                        :max="sellItem.lot" 
                        @change="updateItemCount($event.target.value === '' || $event.target.value === null ? 0 : $event.target.value, this.sellItem.lot)">
                </div>
                <div class="sell-item__remove-item" @click="removeFromCart(sellItem.id)">
                    <div class="item-remove"></div>
                </div>
            </div>
            <div v-else class="sell-item__buy" @click="addToCart(sellItem.id)">
                <svg width="28" height="28" viewBox="0 0 28 30" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M1.875 2.2135H4.80041C5.35867 2.21351 5.90013 2.39678 6.33489 2.73289C6.76964 3.06901 7.07154 3.53773 7.19045 4.06124L7.98155 7.52159M7.98155 7.52159L9.33073 14.6843C9.63218 16.3103 10.5193 17.783 11.8371 18.845C13.1548 19.9071 14.8195 20.491 16.5403 20.4948H19.8409C21.506 20.4914 23.1204 19.9445 24.4188 18.9441C25.7173 17.9436 26.6226 16.549 26.986 14.9894L28.2347 9.68474C28.2964 9.42654 28.2968 9.15833 28.2358 8.89998C28.1748 8.64163 28.0541 8.39975 27.8825 8.19224C27.711 7.98474 27.493 7.81693 27.2447 7.70124C26.9964 7.58554 26.7242 7.52493 26.4482 7.52388L7.98155 7.52159Z" stroke="#FFF" stroke-width="2" stroke-linecap="round"/>
                    <path d="M11.0156 28.4635C11.5765 28.4635 12.0312 28.0088 12.0312 27.4479C12.0312 26.887 11.5765 26.4323 11.0156 26.4323C10.4547 26.4323 10 26.887 10 27.4479C10 28.0088 10.4547 28.4635 11.0156 28.4635Z" stroke="#FFF" stroke-width="2"/>
                    <path d="M25.2344 28.4635C25.7953 28.4635 26.25 28.0088 26.25 27.4479C26.25 26.887 25.7953 26.4323 25.2344 26.4323C24.6735 26.4323 24.2188 26.887 24.2188 27.4479C24.2188 28.0088 24.6735 28.4635 25.2344 28.4635Z" stroke="#FFF" stroke-width="2"/>
                 </svg> 
                <button class="golos-m">{{sellItem.lot === 0 ? ' ' : ' '}}</button>
            </div>
        </div>
    </li>
</template>

<script>
    export default {
        name:'a-sell-item',
        props:{
            sellItem:{
                type: Object,
                default: ()=>{}
            },
            showFav:Boolean,
        },
        methods:{
            addToCart(id){
                this.$store.commit('addToCart', id)
            },
            removeFromCart(id){
                this.$store.commit('removeFromCart', id)
            },
            updateItemCount(newCount, lot){
                this.$store.commit('updateItemCount', {
                    id: this.sellItem.id,
                    count:parseInt(newCount),
                    lot
                })
            },
            addToFav(id){
                this.$store.commit('addToFav', id)
            },
        },
        mounted(){
            console.log()
        }
     
      
       
    }
</script>

<style  scoped>


.sell-item__inner{
    position: relative;
    display: flex;
    flex-direction: column;
    min-height: 100%;
    gap: 20px;
}

.sale{
    position: absolute;
    padding: 7px 12px;
    border-radius: 8px;
    background: #F00;
}

.fav{
    position: absolute;
    right: 0;
}

.sale p{
    color: #FFF;
    font-size: 14px;
}

.sell-item__img img{
    width: 100%;
}
.sell-item__id p:last-child{
    color: #4E4B66;
    font-size: 14px;
    line-height: 16px;
}

.sell-item__price{
    display: flex;
    flex-wrap: wrap-reverse;
    column-gap: 5px;
    align-items: center;
}
.sell-item__price_old{
    color: #A0A3BD;
    font-size: 16px;
    font-weight: 500;
    line-height: normal;
    text-decoration-line: line-through;
}

.sell-item__price_new{
    color: #14142B;
    font-size: 26px;
}

.sell-item__col{
    flex: 1 1 auto;
    display: flex;
    flex-direction: column;
    gap: 8px;
   
}

.sell-item__desc{
    flex: 1 1 auto;
}


.sell-item__buy{
    cursor: pointer;
    padding: 8px 12px;
    display: flex;
    justify-content: center;
    gap: 10px;
    position: relative;
    border-radius: 12px;
    background: #F56E1E;

}
.sell-item__buy:hover{
    background: #DE5703;
}

.sell-item__buy button{
    position: relative;
    color: #FFF;
}


.inactive{
    filter: grayscale(1);
}












.sell-item__buys{
    display: flex;
    justify-content: space-between;
}

.sell-item__count{
    position: relative;
  
}

.sell-item__count input{
    max-width: 52px;
    padding: 8px;
    text-align: center;
    border:2px solid #DE5703;
    border-radius: 20px;
    background-color: #fff;
}


@media (max-width:399px) {
    .sell-item__price_new{
        font-size: 18px;
    }

    .sell-item__desc{
        font-size: 12px;
    }
    .sell-item__buy button{
        font-size: 10px;
    }

    .sell-item__buy svg {
        width: 20px;
        height: 20px;
    }
}

</style>