<template>
    <div class="cards-modal" :class="{showedModal: isOpen}" @click.stop>
        <div class="cards-modal-inner">
            <div class="cards-modal-close" @click="isOpen=false">X</div>
            <div class="cards-modal-content">
                <form class="cards-modal-inputs">
                    <div class="cards-modal-input golos-m" v-for="label,i in labelsArr" :key="i">
                        <input type="text" id="org" v-model="inputsValue" :maxlength="maxLength" required>
                        <label for="org">{{label}}</label>
                    </div>
                </form>
                <div class="cards-modal-add golos-m" >
                    <button  @click="addInfo"></button>
                </div>
                
                
            </div>
        </div>
    </div>
    <div class="cards-modal-overlay overlay" @click="isOpen=false"></div>
    <div class="cards">
        <div class="cards-inner">
            <ul class="cards-items">
                <li class="card-item" v-for="item, i of cards" :key="i">
                    <input class="card-item-checkbox" type="radio" v-model="activeCards" :name="item" :id="i" :value="item">
                    <div class="card-item__title golos-r"> :</div>
                    <div class="card-item__name golos-b">{{item}}</div>
                </li>
                <div class="card-item-add" @click="isOpen=true">
                    <div class="item-add"></div>
                    <p class="card-item-add__text golos-m">{{label}}</p>
                </div>
            </ul>
        </div>
    </div>
</template>

<script>
    export default {
        name:'a-cardgenerate',
        props:{
            labelsArr:{
                type:Array,
                default: ()=>[],
            },
            maxLength: Number,
            label: String,
        },
        computed:{
            getRandom(){
                return Math.random()
            },
            cardWidth(){
                return 100/(this.cards.length + 1)
            }
        },
        data(){
            return{
                isOpen: false,
                cards:[],
                inputsValue:'',
                activeCards:null,
            }
        },
        methods:{
            addInfo(){
                if(this.inputsValue.length === 0 || this.cards.length > 5){
                    this.isOpen = false
                    this.inputsValue=''
                }else{
                    this.cards.push(this.inputsValue)
                }
                this.inputsValue=''
                this.isOpen = false
            }
        }, 
        watch:{
            activeCards(newVal){
                console.log(newVal)
            }
        }
    }
</script>

<style scoped>
 
    .cards-modal{
        position: fixed;
        border-radius: 16px;
        background: #FFF;
        padding: 16px 30px;
        z-index: 600;
        top: -100%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition: top 0.5s ease;
        /* modal */
        box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.30), 0px 4px 8px 3px rgba(0, 0, 0, 0.15);
    }
    .cards-modal-inner{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    .cards-modal-close{
        margin-left: auto;
    }

    .cards-modal-inputs{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .cards-modal-input {
        position: relative;
        display: flex;
        justify-content: space-between;
        padding: 15px 12px;
        border-radius: 12px;
        border: 1px solid #A0A3BC;
        background: #FFF;
        min-width: 305px;
    }

    .cards-modal-input input{
        width: 100%;
        position: relative;
        z-index: 5;
        
    }

    .cards-modal-input label{
        position: absolute;
        top: 15px;
        left: 4px;
        z-index: 0;
        transition: top 0.2s ease;
    }

    .cards-modal-input input:focus + label,
    .cards-modal-input input:valid + label{
        top: 0;
        font-size: 12px;
    }



    .cards-modal-content{
        display: flex;
        flex-direction: column;
        gap: 10px;
    }
    .card-item-add{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .card-item-add:hover .item-add{
        background-color: #E8E8E8;
    }
    .card-item-add:hover .item-add::before,
    .card-item-add:hover .item-add::after{
        background-color:#F56E1E ;
    }
    .card-item-add:hover .card-item-add__text{
        color: #F56E1E;
    }
    .cards-modal-add{
        display: flex;
        justify-content: center;
    }

    .cards-modal-add button{
        flex-basis: 50%;
        padding: 8px 12px;
        border-radius: 12px;
        background: var(--orange, #F56E1E);
        color: #E8E8E8;
        text-align: center;
        transition: all 0.3s ease;
    }

    .cards-modal-add button:hover{
        background: #E8E8E8;
        color: #F56E1E;
    }

    .cards-items{
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
    }
    .card-item,
    .card-item-add{
        position: relative;
        word-wrap: break-word;
        width: 210px;
        height: 94px;
        border-radius: 12px;
        border: 1px solid var(--Gray-07, #A0A3BC);
        padding: 15px;
    }

    .card-item:hover,
    .card-item-add:hover{
        border-color:#F56E1E ;
    }


    .card-item-checkbox{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
    .card-item__title{
        position: relative;
        padding-left: 20px;
    }

    .card-item__title::before{
        content: '';
        position: absolute;
        width: 16px;
        height: 16px;
        top: 0;
        left: 0;
        border-radius: 50%;
        background-color: #C2C4D2;
    }


    .card-item-checkbox:checked + .card-item__title::before{
        background: url('../assets/checked.svg');
        background-size: contain;
        background-repeat: no-repeat;
    }

    @media (max-width: 768px) {
        .cards-items{
            justify-content: center;
        }
    }
    
</style>