<template>
    <div class="slider__pagination">
        <ul class="pagination__items">
            <li class="pagination__item" v-for="item,i in pageCount"  :key="i">
                <div class="pagination__circle"
                    :class="{active: currentSlider === i}"
                    @click="changeSlide(i)"
                ></div>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name:'a-slide-pagination',
        props:{
         sliderItems:{
            type:Array,
            default:()=>[]
         },
         currentSlider: Number,
         pageCount:Number
        },
        data() {
            return {
              
            }
        },
        methods: {
            changeSlide(index) {
                this.$emit('slide-changed', index);
            },
        },
        
       
    }
</script>

<style>
.pagination__items{
    display: flex;
    gap: 5px;
}
.pagination__circle{
    transition: all 0.4s ease;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #C2C4D2;
}

.pagination__circle.active{
    border-radius: 8px;
    width: 22px;
    background-color:#F56E1E;
}

</style>